import React, { memo } from 'react';

import { Dropdown } from 'primereact/dropdown';
import { useQuery } from 'react-query';
import api from '../../services/api';

function CommentsFilter(props) {
    const filter = props.filterComments
    const filters = props.commentsFilters

    const { data: users } = useQuery("users", () => api.get("users").then(res => res.data), { refetchOnWindowFocus: false })
    const { data: products } = useQuery(
        ["products", "has_comments"], 
        () => api.get("products?has_comments=true").then(res => res.data),
        {
            enabled: props.tabName === "Itens",
            refetchOnWindowFocus: false
        }
    )
    const { data: compounds } = useQuery(
        "compounds", 
        () => api.get("compounds").then(res => res.data),
        {
            enabled: props.tabName === "Itens",
            refetchOnWindowFocus: false
        }
    )

    return (
        <>
            <Dropdown 
                value={filters.user}
                options={users} 
                optionLabel="username" optionValue="id"
                onChange={(e) => {
                    filter({...filters, user: e.value})
                }} 
                showClear={true}
                placeholder="Usuário"
            />
            {props.tabName === "Itens" &&
                <>
                    <Dropdown 
                        value={filters.item_product}
                        options={products} 
                        optionLabel="reference" optionValue="id"
                        onChange={(e) => {
                            filter({...filters, item_product: e.value})
                        }} 
                        showClear={true}
                        placeholder="Produto"
                    />
                    <Dropdown 
                        value={filters.item_compound}
                        options={compounds} 
                        optionLabel="name" optionValue="id"
                        onChange={(e) => {
                            filter({...filters, item_compound: e.value})
                        }} 
                        showClear={true}
                        placeholder="Composto"
                    />
                    {filters.item_compound &&
                        <Dropdown 
                            value={filters.item_period}
                            options={compounds.find(compound => compound.id === filters.item_compound).periods} 
                            optionLabel="name" optionValue="id"
                            onChange={(e) => {
                                filter({...filters, item_period: e.value})
                            }} 
                            showClear={true}
                            placeholder="Período"
                        />
                    }
                </>
            }
        </>
    )
}

export default memo(CommentsFilter)