import { useCallback } from 'react'
import useSWR, { useSWRConfig } from 'swr'
import api from '../services/api'

const fetchEstimatives = async (url) => {
    return api.get(url).then(res => res.data)
}

export function useMaterials () {
    const { data, error } = useSWR(
        `/estimatives/materials/`, 
        (url) => api.get(url).then(res => res.data),
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    return {
        materials: data,
        isLoading: !error && !data,
        isError: error
    }
}

export default function useEstimatives (prodId) {
    const { data, error } = useSWR(
        `/estimatives/?product=${prodId}`, 
        (url) => fetchEstimatives(url),
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const logEstimative = useCallback(async (id) => {
        return api.get(`estimatives/${id}/history/`)
        .then(res => res.data)
        .catch(
            err => console.log(err.response)
        )
    }, [])

    return {
        estimatives: data,
        isLoading: !error && !data,
        isError: error,
        logEstimative
    }
}

export function useProductEstimatives (id) {
    const { data, error, mutate } = useSWR(
        `/products/${id}/estimatives`, 
        (url) => api.get(url).then(res => res.data),
        {
            revalidateIfStale: false,
        }
    )

    const addProductEstimative = useCallback(async postData => {
        return api.post(`/products/${id}/estimatives`, postData)
            .then((res) => {
                try {
                    let newColor = {
                        color_id: res.data.color_id,
                        code: res.data.code,
                        description: res.data.description,
                        estimative: res.data.estimative,
                        consumption: res.data.consumption,
                        assortment: res.data.assortment,
                    }
    
                    data.map(entry => {
                        if (entry.material.id === res.data.material) {
                            entry.colors.push(newColor)
                            return entry
                        } else return entry
                    })
                } catch {}
                
                mutate()
                return res
            })
            .catch(err => {
                console.log(err.response)
                alert("erro ao adicionar estimativa")
                return err.response
            })
    }, [])

    return {
        estimatives: data,
        isLoading: !error && !data,
        isError: error,
        addProductEstimative
    }
}

export function useEstimative (id) {
    const { data, error } = useSWR(
        `/estimatives/${id}`, 
        (url) => api.get(url).then(res => res.data),
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )
    const { mutate } = useSWRConfig()

    const patchEstimative = useCallback((patchData) => {
        return api.patch(`estimatives/${id}`, patchData)
            .then(res => {
                mutate(`/estimatives/${id}`)
                return res
            })
            .catch(err => {
                console.log(err.response)
                return err.response
            })
        
    }, [])

    const deleteEstimative = useCallback((prodId=0) => {
        mutate(
            `/products/${prodId}/estimatives`, 
            async (cached) => {
                api.delete(`estimatives/${id}`).catch(err => alert("não foi possível deletar a estimativa"))
                let data = cached.map(entry => {
                    if (entry.colors.find(color => color.estimative === id))
                        return ({
                            ...entry, 
                            colors: entry.colors.filter(color => color.estimative !== id)
                        })
                    return entry
                })

                return data
            }
        )
    }, [])

    return {
        estimative: data,
        isLoading: !error && !data,
        isError: error,
        patchEstimative,
        deleteEstimative
    }
}

export function useMaterial (id) {
    const { data, error, mutate } = useSWR(
        `/estimatives/materials/${id}`,
        (url) => api.get(url).then(res => res.data),
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    return {
        material: data,
        isLoading: !error && !data,
        isError: error
    }
}