import React, { useEffect, useState } from "react";

import "./ViewLogCompound.css";

import { useCompoundItem } from "../../../../hooks/CompoundHandlers";

export default function ViewLogCompound(props) {

	const [logs, setLogs] = useState();
	const { logItem } = useCompoundItem(props.compoundId);

	useEffect(() => {
		logItem().then((res) => setLogs(res));
		props.backdropAutoClose(true);
	}, []);

	const ColoredLine = ({ color }) => (
		<hr
			style={{
				color: color,
				backgroundColor: color,
				height: 3,
				marginTop: 20
			}}
		/>
	);

	return (
		<div className="viewLog">
			{logs?.map((log) =>
				<ul className="lista">
					<li><ColoredLine color="rgb(166, 248, 0)" />{log.history_type === "CREATE" ? `O produto foi criado por meio do método ${log.history_type} pelo usuário ${log.history_user?.username} em ${log.history_date.slice(8, 10) + "/" + log.history_date.slice(5, 7) + "/" + log.history_date.slice(0, 4) + " às " + log.history_date.slice(11, 16)}.` : `O produto foi modificado por meio de ${log.history_type} nos campos listados abaixo pelo usuário ${log.history_user.username} em ${log.history_date.slice(8, 10) + "/" + log.history_date.slice(5, 7) + "/" + log.history_date.slice(0, 4) + " às " + log.history_date.slice(11, 16)}.`}</li>
					{log.history_type === "UPDATE" &&
						<ul>
							{typeof log.changes === "string" ?
								<li className="listaMargin">{log.changes}</li>
								:
								Object.entries(log.changes).map((change) =>
									<li className="listaMargin">
										<span className="coloredTitle">{`${change[0]}`}</span>{`: valor ${change[1].old_value} foi alterado para ${change[1].new_value != "" ? change[1].new_value : "vazio"}.`}
									</li>
								)
							}
						</ul>}
				</ul>
			)}
		</div>
	)
}
