import { Modal } from "@mui/material";
import { confirmDialog } from "primereact/confirmdialog";
import { Dropdown } from "primereact/dropdown";
import { InputText } from 'primereact/inputtext';
import { MultiSelect } from "primereact/multiselect";
import { useState } from "react";

import { useMutation, useQuery, useQueryClient } from "react-query";
import { toast } from 'react-toastify';
import { useDropdownProducts } from "../../../../../hooks/ProductHandlers";
import useProps from "../../../../../hooks/PropsHandlers";
import api from "../../../../../services/api";
import "./InlineFilter.css";


export const InlineFilter = (props) => {
    const [open, setOpen] = useState(false)
    const [details, setDetails] = useState(props.data)
    const client = useQueryClient()

    const { 
        lines,
        groups,
        subgroups,
        tags
    } = useProps()
    const { products } = useDropdownProducts()
    const { data: filter_groups } = useQuery(
        "filter_groups", 
        () => api.get("filters/groups").then(res => res.data),
        {
            enabled: open
        }
    )
    const updateFilter = useMutation(
        (data) => toast.promise(
            () => api.put("filters/" + props.data.id, data),
            {
                pending: "Atualizando filtro...",
                success: "Filtro atualizado!",
                error: "Ocorreu um erro na atualização."
            }
        ),
        {
            onSuccess: (res) => {
                client.invalidateQueries("filters")
            }
        }
    )
    const deleteFilter = useMutation(
        () => toast.promise(
            () => api.delete("filters/" + props.data.id),
            {
                pending: "Deletando filtro...",
                success: "Filtro deletado!",
                error: "Ocorreu um erro na deleção."
            }
        ),
        {
            onSuccess: (res) => {
                client.invalidateQueries("filters")
            }
        }
    )

    const handleClose = () => {
        setOpen(false)
        setDetails(props.data)
    }

    const handleSave = (e) => {
        e.preventDefault()
        updateFilter.mutate(details)
    }

    const handleDelete = () => {
        deleteFilter.mutate()
        setOpen(false)
    }

    const confirmClear = () => {
        confirmDialog({
            message: "Essa ação deletará esse filtro. Continuar?",
            header: "Deletar filtro",
            icon: 'pi pi-exclamation-triangle',
            acceptClassName: 'p-button-danger',
            acceptLabel: "Sim, deletar.",
            rejectLabel: "Não",
            accept: () => handleDelete(),
            reject: () => {}
        });
    }

    return (
        <>
            <div className="inline-filter" onClick={() => setOpen(true)}>
                <p>{details.name}</p>
                <i className="pi pi-trash" onClick={(e) => {
                    e.stopPropagation()
                    confirmClear()
                }}/>
            </div>
            { open &&
                <Modal open={open} onClose={handleClose}>
                    <div id="vf-md-ct">
                        <div id="vf-md">
                            <div className="heading">
                                <h3>{details.name}</h3>
                                <i className="pi pi-times" onClick={() => handleClose()}/>
                            </div>
                            <form className="content">
                                <InputText value={details.name} 
                                    onChange={(e) => setDetails({...details, name: e.target.value})}
                                    placeholder="Nome"
                                />
                                <Dropdown value={details.group} 
                                    onChange={(e) => setDetails({...details, group: e.target.value})}
                                    options={filter_groups}
                                    filter
                                    optionLabel='name'
                                    optionValue="id"
                                    placeholder='Grupo de filtros'
                                />
                                <MultiSelect optionLabel="name" optionValue="id" value={details?.lines} options={lines} filter display='chip'
                                    onChange={(e) => {
                                        setDetails({...details, lines: e.value})
                                    }}
                                    placeholder="Linhas"
                                />
                                <MultiSelect optionLabel="name" optionValue="id" value={details?.groups} options={groups} filter display='chip'
                                    onChange={(e) => {
                                        setDetails({...details, groups: e.value})
                                    }}
                                    placeholder="Grupos"
                                />
                                {details.groups.length !== 0 &&
                                    <MultiSelect optionLabel="name" optionValue="id" value={details?.subgroups} 
                                        options={subgroups?.filter(sub => details.groups.includes(sub.group))} 
                                        filter display='chip'
                                        onChange={(e) => {
                                            setDetails({...details, subgroups: e.value})
                                        }}
                                        placeholder="Subgrupos"
                                    />
                                }
                                <MultiSelect optionLabel="name" optionValue="id" value={details?.tags} options={tags} filter
                                    optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                                    onChange={(e) => {
                                        setDetails({...details, tags: e.value})
                                    }}
                                    placeholder="Tags"
                                />
                                <InputText value={details.size} 
                                    placeholder="Tamanho"
                                    onChange={(e) => setDetails({...details, size: e.target.value})}
                                />
                                <MultiSelect 
                                    optionLabel="reference" optionValue="id" 
                                    value={details?.products} 
                                    options={products} 
                                    filter display='chip'
                                    onChange={(e) => {
                                        setDetails({...details, products: e.value})
                                    }}
                                    placeholder="Produtos"
                                />
                                <button onClick={handleSave}>Salvar</button>
                            </form>
                        </div>
                    </div>
                </Modal>
            }
        </>
    )
}