import { Sidebar } from 'primereact/sidebar';
import { useState } from "react";
import { Mention, MentionsInput } from 'react-mentions';
import { useMutation, useQuery, useQueryClient } from "react-query";
import { toast } from "react-toastify";
import { InlineComment } from "../../../../pages/CommentsPage/InlineComment/InlineComment";
import api from "../../../../services/api";

const getUsers = (query, callback) => {
    api.get("users?username=" + query)
        .then(res => res.data)
        .then(res => res.map(user => ({ display: user.username, id: user.username })))
        .then(callback)
}

export const ProductComments = (props) => {
    const [sideBarOpen, setSideBarOpen] = useState(false)
    const [comment, setComment] = useState("")
    const { data: comments } = useQuery(
        ['productSidebarComments', props.commentsUrl.split("=")[1]], 
        () => api.get(props?.commentsUrl).then(res => res.data),
        {
            enabled: sideBarOpen
        }
    ) 
    const client = useQueryClient()

    const addComment = useMutation(() => api.post("comments/", {
        type: "produto",
        subject: props.commentsUrl.split("=")[1],
        content: comment
    }).then(() => {
        client.invalidateQueries(['productSidebarComments', props.commentsUrl.split("=")[1]])
        toast.success("Comentário adicionado com sucesso!")
        setComment()
    }).catch((err) => {
        toast.error("Ocorreu um erro ao adicionar o comentário")
        console.log(err)
    }))

    return (
        <>
            <i className="pi pi-comments" onClick={() => setSideBarOpen(true)}/>
            <Sidebar visible={sideBarOpen} onHide={() => setSideBarOpen(false)}>
                <div className="add-comment-ct">
                    <MentionsInput
                        placeholder="Mencione pessoas usando '@'"
                        a11ySuggestionsListLabel="Menções sugeridas"
                        className="add-comment-input"
                        value={comment}
                        onChange={(e) => setComment(e.target.value)}
                    >
                        <Mention 
                            displayTransform={mention => `@${mention}`}
                            data={getUsers} className="mention"
                            trigger="@"
                            markup="@__id__"
                            regex={RegExp(/@([\.\w\u00E7\u00E3]+)(?:\s)/)}
                            appendSpaceOnAdd
                        />
                    </MentionsInput>
                    <i className="pi pi-send" onClick={() => addComment.mutate()}/>
                </div>
                {comments ?
                    comments.map(comment => <InlineComment 
                        comment={comment} key={comment.id} 
                        noCaller onDeleteInvalidateKey={'productSidebarComments'}
                    />)
                    :
                    <i className="pi pi-spin pi-spinner" />
                }
            </Sidebar>
        </>
    )
}