import React, { useEffect, useState } from 'react';
import useCompounds from '../../../../hooks/CompoundHandlers';


import { ProductCard } from '../../../ProductCard/ProductCard';
import ModalBase from '../../../shared/ModalBase/ModalBase';
import AddProduct from '../../Products/AddProduct/AddProduct';
import ViewProduct from '../../Products/ViewProduct/ViewProduct';

import { ButtonBase } from '@mui/material';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import AsyncSelect from 'react-select/async';
import { useSWRConfig } from "swr";

import api from '../../../../services/api';
import './AddProductCompound.css';

export default function AddProductCompound(props) {
    const { mutate } = useSWRConfig()
    const { compounds, addItem } = useCompounds()
    const [details, setDetails] = useState({
        product: "",
        observations: "",
        period: props.period ? props.period : "",
    })
    const [selected, setSelected] = useState({})

    useEffect(() => {
        props.backdropAutoClose(false)
        setSelected(compounds.find(compound => compound.id === props.compoundId))
    }, [])

    const handleSave = () => {
        const postData = {
            product: details.product.id,
            observations: details.observations,
            period: details.period.id,
        }

        addItem(postData, () => {
            mutate(`compounds/items`)
            props.closeModal()
        })
    }

    const handleCancel = () => {
        setDetails({
            product: "",
            observations: "",
            period: "",
        })
    }

    const setCompoundProduct = (value) => {
        mutate("/products/")
        api.get(`products/${value.id}/`).then(res => {
            if (res.status === 200) {
                setDetails({ ...details, product: res.data })
            }
        }).catch(() => {
            alert("Ocorreu um problema ao retirar o novo produto, por favor selecione-o manualmente.")
        })
    }

    const loadOptions = (inputValue, callback) => {
        api.get(
            `products/dropdown?limit=15&search=${inputValue}`
        ).then(res => callback(res.data.results))
    };

    return (
        <div className='addProductCompound'>
            <div className="p-grid">
                <div className="p-col">
                    <div className='productContainer'>
                        {details.product ? (
                            <ModalBase
                                caller={
                                    <ProductCard
                                        prodId={details.product.id}
                                        key={details.product.reference}
                                    />
                                }
                                content={<ViewProduct productId={details.product.id} />}
                                title={details.product.reference}
                            />
                        ) : (
                            <h3>Selecione um PRODUTO para exibir...</h3>
                        )}
                    </div>
                </div>
                <div className="p-col">
                    <div className='detailsContainer p-grid p-dir-col'>
                        <div className="pcol">
                            <div className="p-grid p-dir-col">
                                <div className="p-col details--product">
                                    <h4>Produto</h4>
                                    <AsyncSelect onChange={(selected) => setDetails({ ...details, product: selected })}
                                        styles={{
                                            container: (baseStyles, state) => ({
                                              ...baseStyles,
                                              width: "100%",
                                              height: "2.5rem"
                                            }),
                                            option: (baseStyles, state) => ({
                                                ...baseStyles,
                                                color: "black",
                                              }),
                                          }}
                                        cacheOptions placeholder="Selecionar produto"
                                        loadOptions={loadOptions}
                                        defaultOptions
                                        getOptionLabel={(obj) => obj.reference}
                                        getOptionValue={(obj) => obj.id}
                                    />
                                    <p>ou</p>
                                    <ModalBase
                                        caller={<div className='details--createProduct'>Criar novo produto</div>}
                                        content={<AddProduct setNewProduct={setCompoundProduct} />}
                                        title="Adicionar PRODUTO ao COMPOSTO"
                                    />
                                </div>
                                <div className="p-col">
                                    <h4>Período</h4>
                                    <Dropdown value={details.period}
                                        onChange={(e) => setDetails({ ...details, period: e.target.value })}
                                        options={selected?.periods}
                                        optionLabel='name'
                                        placeholder='Selecione um período'
                                    />
                                </div>
                                <div className="p-col">
                                    <h4>Observações</h4>
                                    <InputText value={details.observations}
                                        onChange={(e) => setDetails({ ...details, observations: e.target.value })}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="pcol buttons">
                            <div className="p-grid p-dir-col">
                                <div className="p-col">
                                    <ButtonBase className='button clear'
                                        onClick={() => {
                                            handleCancel()
                                        }}
                                    >Limpar</ButtonBase>
                                </div>
                                <div className="p-col">
                                    <ButtonBase className='button save'
                                        onClick={() => {
                                            handleSave()
                                        }}>Adicionar</ButtonBase>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}