import { useCallback, useState } from 'react'
import { useInfiniteQuery, useQuery, useQueryClient } from 'react-query'
import { toast } from 'react-toastify'
import useSWR from 'swr'
import api from '../services/api'
import { juntarArrays } from '../utils/arrays'

const fetchProducts = async (url, filters) => {
    const fetchUrl = url + getFilteredUrl(filters)
    return api.get(fetchUrl).then(res => res.data)
}

const fetchProduct = (url) => api.get(url).then(res => res.data)

function getFilteredUrl (filters, url="?") {
    let tags = juntarArrays(
        filters.product_tags,
        filters.process_tags
    )

    url += filters.noPhase !== false ? "&no_phase=true" : ""
    url += checkFilter("search", filters.reference)
    if (tags.length !== 0) url += `&tags=${tags?.map(tag => tag.id).join(',')}`;
    url += checkFilter("phase", filters.phase)
    url += checkFilter("typed", filters.typed)
    url += checkFilter("erp", filters.erp)
    url += checkFilter("showcase", filters.showcase)
    url += checkFilter("generic", filters.generic)
    url += checkFilter("material", filters.material)
    url += checkFilter("store", filters.store)
    url += checkFilter("period", filters.period)
    url += checkFilter("line", filters.line)
    url += checkFilter("group", filters.group)
    url += checkFilter("subgroup", filters.subgroup)
    url += checkFilter("collection", filters.collection)
    url += checkFilter("compound", filters.compound)
    url += checkFilter("period", filters.period)
    url += checkFilter("stylist", filters.stylist)
    url += checkFilter("designer", filters.designer)
    url += checkFilter("limit", filters.limit)
    url += checkFilter("offset", filters.offset)

    if (filters?.filter_type === "AND") {
        url += "&filter_type=AND"
    }

    return url
}

function getFilteredNotPaginatedUrl (baseUrl, filters) {
    let url = baseUrl + "?"
    let tags = juntarArrays(
        filters.product_tags,
        filters.process_tags
    )

    url += filters.noPhase !== false ? "&no_phase=true" : ""
    url += checkFilter("search", filters.reference)
    if (tags.length !== 0) url += `&tags=${tags?.map(tag => tag.id).join(',')}`;
    url += checkFilter("phase", filters.phase)
    url += checkFilter("typed", filters.typed)
    url += checkFilter("erp", filters.erp)
    url += checkFilter("showcase", filters.showcase)
    url += checkFilter("generic", filters.generic)
    url += checkFilter("material", filters.material)
    url += checkFilter("store", filters.store)
    url += checkFilter("period", filters.period)
    url += checkFilter("line", filters.line)
    url += checkFilter("group", filters.group)
    url += checkFilter("subgroup", filters.subgroup)
    url += checkFilter("collection", filters.collection)
    url += checkFilter("compound", filters.compound)
    url += checkFilter("period", filters.period)
    url += checkFilter("stylist", filters.stylist)
    url += checkFilter("designer", filters.designer)

    return url
}

function checkFilter (key, value) {
    if (value !== "" && value !== undefined && value !== null && value.length !== 0) {
        return `&${key}=${value}`
    }
    return ""
}

export default function useProducts (options={paginated: true, defaultFilters: null}) {
    const baseFilters = {
        reference: "",
        product_tags: "",
        process_tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        compound: "",
        period: "",
        material: "",
        stylist: "",
        designer: "",
        noPhase: false,
        erp: null,
        showcase: null,
        generic: null,
        limit: options.paginated ? 12 : 0,
        offset: options.paginated ? 0 : null,
        filter_type: "OR"
    }
    const [filters, setFilters] = useState(options.defaultFilters ? options.defaultFilters : baseFilters)
    const { data, error, mutate } = useSWR(
        ['/products/', filters],
        fetchProducts,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const filterProducts = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }

            if (key === "compound" && is_null) {
                updatedValue["period"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    const addProduct = useCallback((
            postData, image="", 
            callback=()=>{}, closeModal=()=>{}
        ) => {
        mutate(async () => {
            api.post('/products/', postData).then(res => {
                if (res.status === 200) {
                    if (image !== "") addImage(res.data.id, image)
                    else alert("Produto criado com sucesso!")
                    callback(res)
                    closeModal()
                }
            }).catch(err => {
                console.log(err)
                alert("ocorreu um erro ao processar a solicitação")
            })
        })
    }, [])

    const addImage = useCallback(async (id, image) => {
        mutate(async () => {
            const postData = new FormData()
            postData.append("product", id)
            postData.append("image", image)

            api.post("products/images", postData).then((res) => {
                mutate()
                alert("Produto criado com sucesso!")
            }).catch(err => console.log(err.response))
        })
    }, [])

    return {
      products: data,
      productFilters: filters,
      isLoading: !error && !data,
      isError: error,
      filterProducts,
      addProduct,
      updateProducts: mutate
    }
}

export function useProduct (id) {
    const { data, error, mutate } = useSWR(
        `/products/${id}/`, 
        fetchProduct,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )
    const { updateProducts } = useProducts()

    const patchProduct = useCallback(async (patchData) => {
        return api.patch(`/products/${id}/`, patchData)
            .then(() => {
                mutate() 
            })
            .catch(err => {
                console.log(err.response)
                return err.response
            })
    }, [id, mutate])

    const logProduct = useCallback(() => {
        return api.get(`/products/${id}/history`).then(res => res.data)
    }, [])

    const deleteProduct = useCallback(() => {
        api.delete(`/products/${id}/`).then(() => {
            mutate()
            updateProducts()
        })
    }, [id, mutate, updateProducts])

    return {
        product: data,
        isLoading: !error && !data,
        isError: error,
        patchProduct,
        deleteProduct,
        logProduct,
        updateProduct: mutate
    }
}

export const getFilteredExport = (filters) => {
    let filteredUrl = getFilteredNotPaginatedUrl("products/export", filters)
    toast.promise(
        api.get(filteredUrl, {responseType: "blob", timeout: 8 * 60 * 1000}).then(response => {
            let url = window.URL.createObjectURL(response.data);
            let a = document.createElement('a');
            a.href = url;
            a.download = 'greenmix_products.xlsx';
            a.click();
        }).catch(err => {
            if (err.code == "ECONNABORTED") {
                alert("Tempo máximo de requisição atingido!")
            }
        }),
        {
            pending: "Exportando produtos...",
            success: "Produtos exportados com sucesso!",
            error: "Ocorreu um erro na exportação dos produtos!"
        }
    )
    
}

export const useDropdownProducts = (filters={}) => {
    let url = "products/dropdown?"

    if (filters.noPhase) {
        url += "no_phase=" + filters.noPhase
    }

    const { data, isLoading, isError } = useQuery(
        "dropdown_product",
        () => api.get(url).then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )
    

    return {
        products: data,
        isLoading,
        isError
    }
}

export const useInfiniteProducts = () => {
    const baseFilters = {
        reference: "",
        product_tags: "",
        process_tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        compound: "",
        period: "",
        material: "",
        stylist: "",
        designer: "",
        noPhase: false,
        erp: null,
        showcase: null,
        generic: null,
        filter_type: "OR"
    }
    const [filters, setFilters] = useState(baseFilters)
    const qClient = useQueryClient()

    let url = `products?`
    let page_size = 20

    url = url + `&limit=${page_size}&offset=`

    const fetchPages = async ({ pageParam = 0 }) => api.get(url + pageParam + getFilteredUrl(filters, "")).then(res => res.data)

    const {
        data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
        isLoading,
    } = useInfiniteQuery(['infinite_products', filters], fetchPages, {
        getNextPageParam: (lastPage, pages) => lastPage.next ? pages.length * page_size : undefined,
        refetchOnWindowFocus: false
    })

    const filterProducts = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }

            if (key === "compound" && is_null) {
                updatedValue["period"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    const addProduct = useCallback((
            postData, image="", 
            callback=()=>{}, closeModal=()=>{}
        ) => {
        mutate(async () => {
            api.post('/products/', postData).then(res => {
                if (res.status === 200) {
                    if (image !== "") addImage(res.data.id, image)
                    else alert("Produto criado com sucesso!")
                    callback(res)
                    closeModal()
                }
            }).catch(err => {
                console.log(err)
                alert("ocorreu um erro ao processar a solicitação")
            })
        })
    }, [])

    const addImage = useCallback(async (id, image) => {
        mutate(async () => {
            const postData = new FormData()
            postData.append("product", id)
            postData.append("image", image)

            api.post("products/images", postData).then((res) => {
                mutate()
                alert("Produto criado com sucesso!")
            }).catch(err => console.log(err.response))
        })
    }, [])

    const updateProducts = () => qClient.invalidateQueries(['infinite_products'])

    return {
        products: data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isLoading,
        isFetchingNextPage,
        status,
        productFilters: filters,
        filterProducts,
        addProduct,
        addImage,
        updateProducts
    }
}