import React, { memo } from 'react';

import CompoundsFilters from '../SideBarFilters/CompoundsFilters';
import ProductFilters from '../SideBarFilters/ProductFilters';

import { useLocation, useNavigate } from "react-router-dom";

import PhasesFilter from '../SideBarFilters/PhasesFilter';
import './SideBar.css';

import DashboardIcon from '@mui/icons-material/Dashboard';
import FilterListIcon from '@mui/icons-material/FilterList';
import LogoutRoundedIcon from '@mui/icons-material/LogoutRounded';
import { ButtonBase } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { logout, selectUser } from '../../app/slices/userSlice';
import CommentsFilter from '../SideBarFilters/CommentsFilter';

function AsideBar(props) {
    const navigate = useNavigate()
    const location = useLocation()
    const user = useSelector(selectUser)
    const dispatch = useDispatch()

    const Filters = () => {
        switch (location.pathname) {
            case "/products":
                return <ProductFilters 
                    productFilters={props.productFilters} 
                    filterProducts={props.filterProducts}
                />
            case "/phases":
                return <PhasesFilter 
                    phaseFilters={props.phaseFilters}
                    filterPhases={props.filterPhases}
                />
            case "/compounds":
                return <CompoundsFilters 
                    periodHooks={props.periodHooks}
                />
            case "/comments":
                return <CommentsFilter 
                    commentsFilters={props.commentsFilters}
                    filterComments={props.filterComments}
                    tabName={props.tabName}
                />
        }
    }

    const log_out = () => {
        localStorage.removeItem("@greenMix:TOKENS")
        localStorage.removeItem("@greenmix:PERIOD_FILTERS")
        localStorage.removeItem("@greenMix:SELECTED_COMPOUND")
        dispatch(logout({}))
        navigate('/', {replace: true})
    }

    return(
        <div className={`sideBar`}>
            <div className='identification'>
                <h2>Olá, {user.username ? user.username.charAt(0).toUpperCase() + user.username.slice(1) : "User"}</h2>
            </div>
            <div className='dashboard'>
                <div style={{display: "flex", gap: "0.25rem"}}>
                    <DashboardIcon />
                    <h4>Dashboard</h4>
                </div>
                <a onClick={() => navigate(`/products`)}>Produtos</a>
                <a onClick={() => navigate(`/phases`)}>Fases</a>
                <a onClick={() => navigate(`/compounds`)}>Compostos</a>
                <span>
                    <a onClick={() => navigate(`/comments`)}>Comentários</a>
                    {user.unread_comments?.length !== 0 && <span className="p-badge">{user.unread_comments.length}</span>}
                </span>
                <a onClick={() => navigate(`/filters`)}>GreenParams</a>
            </div>
            {!props.noFilters &&
                <div className='filters'>
                    <div style={{display: "flex", gap: "0.25rem"}}>
                        <FilterListIcon />
                        <h4>Filtros</h4>
                    </div>
                    <Filters />
                </div>
            }
            <ButtonBase className='logout' onClick={log_out}>
                <LogoutRoundedIcon /> Logout
            </ButtonBase>
        </div>
    )
}

export default memo(AsideBar)