import { useState } from "react"
import { Modal } from "@mui/material"
import { InputNumber } from "primereact/inputnumber";

import "./EditMetric.css"
import { useMutation, useQueryClient } from "react-query";
import api from "../../../../../services/api";
import { toast } from 'react-toastify';


export const EditMetric = (props) => {
    const [open, setOpen] = useState(false)
    const [details, setDetails] = useState({
        max_value: props.data.max_value,
        min_value: props.data.min_value
    })
    const client = useQueryClient()

    const editMetric = useMutation(() => api.patch("filters/parametrics/" + props.data.id, details)
        .then(() => {
            client.invalidateQueries("params")
            handleClose()
            toast.success("Métrica alterada com sucesso!")
        }).catch((err) => toast.error("Não foi possível alterar a métrica!"))
    )

    const handleClose = () => {
        setOpen(false)
    }

    const handleSave = (e) => {
        e.preventDefault()
        editMetric.mutate()
    }

    return (
        <>
            <i onClick={() => setOpen(true)} className="pi pi-pencil" />
            { open &&
                <Modal open={open} onClose={handleClose}>
                    <div id="editm-md-ct">
                        <div id="editm-md">
                            <div className="heading">
                                <h3>Editar métrica</h3>
                                <i className="pi pi-times" onClick={() => handleClose()}/>
                            </div>
                            <form className="content">
                                <h4>Valor mínimo</h4>
                                <InputNumber
                                    placeholder="Mínimo"
                                    value={details.min_value}
                                    locale="pt-BR"
                                    showButtons
                                    min={0}
                                    onValueChange={(e) => {
                                        setDetails({
                                            ...details,
                                            min_value: e.value,
                                        });
                                    }}
                                />
                                <h4>Valor máximo</h4>
                                <InputNumber
                                    placeholder="Máximo"
                                    value={details.max_value}
                                    locale="pt-BR"
                                    showButtons
                                    min={0}
                                    onValueChange={(e) => {
                                        setDetails({
                                            ...details,
                                            max_value: e.value,
                                        });
                                    }}
                                />
                                <button onClick={handleSave}>Salvar</button>
                            </form>
                        </div>
                    </div>
                </Modal>
            }
        </>
    )
}