import { useCallback, useEffect, useState } from 'react'
import { useInfiniteQuery, useQuery, useQueryClient } from 'react-query'
import api from '../services/api'
import { juntarArrays } from '../utils/arrays'

function getFilteredUrl (url, filters) {
    const tags = juntarArrays(
        filters.product_tags,
        filters.process_tags
    )

    url += checkFilter("product", filters.product)
    if (tags.length !== 0) url += `&tags=${tags?.map(tag => tag.id).join(',')}`;
    url += checkFilter("phase", filters.phase)
    url += checkFilter("typed", filters.typed)
    url += checkFilter("erp", filters.erp)
    url += checkFilter("generic", filters.generic)
    url += checkFilter("material", filters.material)
    url += checkFilter("store", filters.store)
    url += checkFilter("period", filters.period)
    url += checkFilter("line", filters.line)
    url += checkFilter("group", filters.group)
    url += checkFilter("subgroup", filters.subgroup)
    url += checkFilter("collection", filters.collection)
    url += checkFilter("stylist", filters.stylist)
    url += checkFilter("designer", filters.designer)
    url += checkFilter("ordering", filters.ordering)
    url += checkFilter("non_local_tag", filters.non_local_tag)
        
    return url
}

function checkFilter (key, value) {
    if (value !== "" && value !== undefined && value !== null && value.length !== 0) {
        return `&${key}=${value}`
    }
    return ""
}

export const useInfinitePeriodItems = (periodId) => {
    const [filters, setFilters] = useState({
        product: "",
        phase: "",
        store: "",
        period: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        stylist: "",
        designer: "",
        product_tags: "",
        process_tags: "",
        material: "",
        typed: null,
        erp: null,
        generic: null,
        ordering: null,
        non_local_tag: null
    })
    const client = useQueryClient()

    useEffect(() => {
        const stored_filters = JSON.parse(localStorage.getItem("@greenmix:PERIOD_FILTERS"))
        if (stored_filters) {
            stored_filters.product = ""
            setFilters(stored_filters)
        }
    }, [])

    useEffect(() => {
        localStorage.setItem("@greenmix:PERIOD_FILTERS", JSON.stringify(filters))
    }, [filters])

    let url = `compounds/items?period=${periodId}`
    let page_size = 20

    url = url + `&limit=${page_size}&offset=`

    const fetchPages = async ({ pageParam = 0 }) => api.get(getFilteredUrl(url + pageParam, filters)).then(res => res.data)

    const {
        data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
    } = useInfiniteQuery(['infinite_period_items', periodId, filters], fetchPages, {
        getNextPageParam: (lastPage, pages) => lastPage.next ? pages.length * page_size : undefined,
        refetchOnWindowFocus: false
    })

    const filterItems = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    const copyItem = useCallback(itemId => {
        api.post("compounds/items/duplicate", {item: itemId})
            .then(res => {mutate()})
            .catch(err => {console.log(err.response)})
    }, [])

    const updateItems = () => client.invalidateQueries('infinite_period_items')

    return {
        items: data,
        updateItems,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
        filters,
        filterItems,
        copyItem
    }
}

export const usePeriodDemandsSum = (periodId, filters) => {
    let url = `compounds/periods/${periodId}/demands_sum?`

    const { data } = useQuery(
        ["period_demands_sum", periodId, filters],
        () => api.get(getFilteredUrl(url, filters)).then(res => res.data.sum),
        {
            refetchOnWindowFocus: false,
        }
    )

    return {
        data
    }
}