import { Modal } from "@mui/material";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";
import { useState } from "react";

import { useMutation, useQuery, useQueryClient } from "react-query";
import { toast } from 'react-toastify';
import api from "../../../../../services/api";
import "./AddMetricModal.css";

export const AddMetricModal = (props) => {
    const [open, setOpen] = useState(false)
    const [details, setDetails] = useState({
        param: props.param,
        metric: null,
        min: null,
        max: null
    })
    const client = useQueryClient()

    const { data: metrics } = useQuery(["metrics", "param", props.param], () => api.get("filters/metrics?not_in_param=" + props.param).then(res => res.data))

    const handleClose = () => {
        setOpen(false)
        setDetails({
            param: props.param,
            metric: null,
            min: null,
            max: null
        })
    }

    const saveMetric = useMutation(() => api.post("filters/params/" + props.param, details).then(() => {
        client.invalidateQueries("params")
        toast.success("Métrica adicionada com sucesso!")
        handleClose()
    }).catch((err) => toast.error("Não foi possível criar a métrica!")))

    const handleSave = (e) => {
        e.preventDefault()
        saveMetric.mutate()
    }

    return (
        <>
            <i className="pi pi-plus addm-caller" onClick={() => setOpen(true)} />
            {open &&
                <Modal open={open} onClose={handleClose}>
                    <div id="addm-md-ct">
                        <div id="addm-md">
                            <div className="heading">
                                <h3>Adicionar métrica</h3>
                                <i className="pi pi-times" onClick={() => handleClose()} />
                            </div>
                            <form className="content">
                                <Dropdown value={details.metric}
                                    onChange={(e) => setDetails({ ...details, metric: e.target.value })}
                                    options={metrics}
                                    filter
                                    optionLabel='name'
                                    optionValue="id"
                                    placeholder='Métrica'
                                />
                                <div style={{display: "flex", gap: "1.5rem"}}>
                                    <InputNumber
                                        placeholder="Mínimo"
                                        value={details.min}
                                        locale="pt-BR"
                                        showButtons
                                        min={0}
                                        onValueChange={(e) => {
                                            setDetails({
                                                ...details,
                                                min: e.value,
                                            });
                                        }}
                                    />
                                    <InputNumber
                                        placeholder="Máximo"
                                        value={details.max}
                                        locale="pt-BR"
                                        showButtons
                                        min={0}
                                        onValueChange={(e) => {
                                            setDetails({
                                                ...details,
                                                max: e.value,
                                            });
                                        }}
                                    />
                                </div>
                                <button onClick={handleSave}>Salvar</button>
                            </form>
                        </div>
                    </div>
                </Modal>
            }
        </>
    )
}