import { useCallback } from 'react'
import { useQueryClient } from 'react-query'
import useSWR from 'swr'
import api from '../services/api'

const fetchDemands = async (url) => api.get(url).then(res => res.data)

export function useDemands (itemId) {
    const { data, error, mutate } = useSWR(
        `/compounds/items/demands?item=${itemId}`, 
        fetchDemands,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const addDemand = useCallback(async (itemId, storeId, quantity, date, production_date) => {
        return api.post("/compounds/items/demands", {
            item: itemId,
            store: storeId,
            quantity: quantity,
            date: date,
            production_date: production_date
        }).then((res) => {
            mutate()
            return res
        }).catch((err) => {
            console.log(err.response)
            return err.response
        })
    }, [])

    const updateDemand = useCallback(async (demandId, storeId, quantity, date) => {
        return api.patch(`/compounds/items/demands/${demandId}`, {
            store: storeId,
            quantity: quantity,
            date: date,
            production_date: production_date
        }).then((res) => {
            mutate()
            return res
        }).catch((err) => {
            console.log(err.response)
            return err.response
        })
    }, [])

    const bookUpdateDemand = useCallback(async (demands) => {
        return demands.map((demand) => {

            return api.patch(`/compounds/items/demands/${demand.id}`, {
                store: demand.store.id,
                quantity: demand.quantity,
                date: demand.date,
                production_date: demand.production_date,
            }).then((res) => {
                mutate()
                return res
            }).catch((err) => {
                console.log(err.response)
                return err.response
            })
            
        })
    }, [])

    const deleteDemand = useCallback(async id => {
        return api.delete(`compounds/items/demands/${id}`).then((res) => {
            mutate()
            return res
        }).catch((err) => {
            console.log(err.response)
            alert("Operação não concluída.")
            return err.response
        })
    }, [])

    const logDemand = useCallback(async id => {
        return api.get(`compounds/items/demands/${id}/history`)
        .then(res => res.data)
        .catch(
            err => console.log(err.response)
        )
    }, [])

    return {
        demands: data,
        isLoading: !error && !data,
        isError: error,
        addDemand,
        deleteDemand,
        updateDemand,
        logDemand,
        bookUpdateDemand
    }
}

export default function useStores() {
    const { data, error } = useSWR(
        "/compounds/stores", 
        fetchDemands,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    return {
        stores: data,
        isLoading: !error && !data,
        isError: error
    }
}

