import { ParamContainer } from "../ParamContainer/ParamContainer"
import "./GroupContainer.css"

export const GroupContainer = (props) => {
    return (
        <div className="g-ct">
            <h3>{props.group}</h3>
            {
                props.params.map(param => <ParamContainer data={param} key={param.id} />)
            }
        </div>
    )
}