import { useCallback, useState } from 'react'
import { useInfiniteQuery, useQueryClient } from 'react-query'
import useSWR from 'swr'
import api from '../services/api'
import { juntarArrays } from '../utils/arrays'

const fetchPhases = async (url, filters) => {
    const fetchUrl = url + getFilteredUrl(filters)
    return api.get(fetchUrl).then(res => res.data)
}

function getFilteredUrl (filters, url="?") {
    url += filters.product !== "" ? `&product=${filters.product}` : ""
    url += filters.line !== "" ? `&line=${filters.line}` : ""
    url += filters.group !== "" ? `&group=${filters.group}` : ""
    url += filters.subgroup !== "" ? `&subgroup=${filters.subgroup}` : ""
    url += filters.collection !== "" ? `&collection=${filters.collection}` : ""
    url += filters.compound !== "" ? `&compound=${filters.compound}` : ""

    return url
}

function getFilteredProducts (filters, url="?") {
    let tags = juntarArrays(
        filters.product_tags,
        filters.process_tags
    )

    url += checkFilter("search", filters.reference)
    if (tags.length !== 0) url += `&tags=${tags?.map(tag => tag.id).join(',')}`;
    url += checkFilter("phase", filters.phase)
    url += checkFilter("typed", filters.typed)
    url += checkFilter("erp", filters.erp)
    url += checkFilter("showcase", filters.showcase)
    url += checkFilter("ordering", filters.ordering)
    url += checkFilter("generic", filters.generic)
    url += checkFilter("material", filters.material)
    url += checkFilter("store", filters.store)
    url += checkFilter("period", filters.period)
    url += checkFilter("line", filters.line)
    url += checkFilter("group", filters.group)
    url += checkFilter("subgroup", filters.subgroup)
    url += checkFilter("collection", filters.collection)
    url += checkFilter("stylist", filters.stylist)
    url += checkFilter("designer", filters.designer)
    url += checkFilter("compound", filters.compound)
    url += checkFilter("period", filters.period)

    return url
}

function checkFilter (key, value) {
    if (value !== "" && value !== undefined && value !== null) {
        return `&${key}=${value}`
    }
    return ""
}

export default function usePhases () {
    const [filters, setFilters] = useState({
        reference: "",
        product_tags: "",
        process_tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        noPhase: false,
        erp: null,
        showcase: null,
        generic: null
    })

    const { data, error, mutate } = useSWR(
        ['/phases/', filters], 
        fetchPhases,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const filterPhases = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    const removeProduct = useCallback((id) => {
        api.patch(`products/${id}/`, {phase: null}).then(() => {
            mutate()
        })
    }, [])

    const patchProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            mutate()
            return res
        }).catch((err) => err.response)
    })

    const addProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            mutate()
            return res
        }).catch((err) => err.response)
    })

    return {
        phases: data,
        phasesFilters: filters,
        isLoading: !error && !data,
        isError: error,
        updatePhases: mutate,
        filters,
        filterPhases,
        removeProduct,
        patchProduct,
        addProduct
    }
}

export function usePhaseFilters () {
    const [filters, setFilters] = useState({
        reference: "",
        product_tags: "",
        process_tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        period: "",
        noPhase: false,
        erp: null,
        showcase: null,
        generic: null,
        ordering: "reference",
    })

    const filter = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    return {
        filters,
        filter
    }
}

export function usePhaseProducts (id) {
    const [filters, setFilters] = useState({
        reference: "",
        product_tags: "",
        process_tags: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        material: "",
        stylist: "",
        designer: "",
        compound: "",
        period: "",
        ordering: "reference",
        noPhase: false,
        erp: null,
        showcase: null,
        generic: null
    })

    const page_size = 10
    const fetchPages = ({ pageParam = 0 }) => api.get(
        `products?phase=${id}` + getFilteredProducts(filters, "") + `&limit=${page_size}&offset=${pageParam}`
    ).then(res => res.data)

    const {
        data,
        error,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
    } = useInfiniteQuery(
        ["phase", id, filters], 
        fetchPages, 
        {
            getNextPageParam: (lastPage, pages) => lastPage.next ? pages.length * page_size : undefined,
            refetchOnWindowFocus: false,
        }
    )
    const qClient = useQueryClient()

    const filterPhase = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    const removeProduct = useCallback(async (prodId) => {
        return api.patch(`products/${prodId}/`, {phase: null}).then((res) => {
            qClient.invalidateQueries(["phase", id])
            return res
        })
    }, [])

    const patchProduct = useCallback(async (product, phase, showcase, newModeling, limitDate) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id, showcase, new_modeling: newModeling, limit_date: limitDate}
        ).then((res) => {
            qClient.invalidateQueries(["phase", id])
            qClient.invalidateQueries(["phase", phase.id])
            return res
        })
    }, [])

    const addProduct = useCallback(async (product, phase) => {
        return api.patch(
            `products/${product.id}/`, 
            {phase: phase.id}
        ).then((res) => {
            qClient.invalidateQueries(["phase", phase.id])
            return res
        }).catch((err) => err.response)
    }, [])

    return {
        products: data,
        isLoading: !error && !data,
        isError: error,
        updatePhase: () => qClient.invalidateQueries(["phase", id]),
        filterPhase,
        setFilters,
        fetchNextPage,
        hasNextPage,
        isFetching,
        isFetchingNextPage,
        status,
        phaseOperations: {
            add: addProduct,
            remove: removeProduct,
            patch: patchProduct
        }
    }
}