import 'primeflex/primeflex.css';
import React, { useEffect, useState } from 'react';
import './style.css';

import { TextField } from '@mui/material';
import { Dropdown } from 'primereact/dropdown';

import InsertDriveFileOutlinedIcon from '@mui/icons-material/InsertDriveFileOutlined';
import SideBar from '../../components/SideBar/SideBar';

import MobileSidebar from '../../components/MobileSidebar/MobileSidebar';
import ImportSheets from '../../components/Modals/Compounds/ImportSheets/ImportSheets';
import ModalBase from '../../components/shared/ModalBase/ModalBase';
import useCompounds from '../../hooks/CompoundHandlers';
import { isAuthenticated } from '../../services/api';
import CompoundsPageContent from '../CompoundsPageContent';

const CompoundsPage = () => {
    useEffect(() => {
        isAuthenticated()
    }, [])

    const { 
        compounds, 
        isLoading,
        isError,
        updateCompounds
    } = useCompounds()

    const [selectedCompound, setSelectedCompound] = useState({})
    const [periodHooks, setHooks] = useState()
    const [mobile, setMobile] = useState(false)

    const [search, setSearch] = useState("");
    const [debouncedValue, setDebouncedValue] = useState("");

    useEffect(() => {
        const debounceTimer = setTimeout(() => {
            setDebouncedValue(search);
        }, 800);

        return () => {
            clearTimeout(debounceTimer);
        };
    }, [search]);

    useEffect(() => {
        if (debouncedValue || debouncedValue === "") {
            periodHooks?.setFilters("product", debouncedValue)
        }
    }, [debouncedValue])

    useEffect(() => {
        const handleResize = () => {
            if (document.documentElement.clientWidth < 960) {
                setMobile(true)
            } else setMobile(false)
        }


        handleResize()
        window.addEventListener('resize', handleResize)

        return () => window.removeEventListener('resize', handleResize)
    }, [])

    useEffect(() => {
        if (!isError && compounds) {
            const selected = JSON.parse(localStorage.getItem("@greenmix:SELECTED_COMPOUND"))
            if (!selected) {
                setSelectedCompound(compounds[0])
            } else {
                setSelectedCompound(compounds?.find(compound => compound.id === selected.id))
            }
        }
    }, [compounds])

    return (
        <div className='compoundPage page'>
            {!mobile &&
                <SideBar 
                    periodHooks={periodHooks ? periodHooks : null}
                />
            }
            <div className='compoundPage--body'>
                <div className='header'>
                    <div className='header--content'>
                        <div className='header-title--container'>
                            {mobile ? 
                                <MobileSidebar 
                                    periodHooks={periodHooks ? periodHooks : null}
                                />
                                :
                                <h2>Estes são os itens de</h2>
                            }
                            <Dropdown value={selectedCompound} 
                                onChange={(e) => {
                                    setSelectedCompound(e.target.value)
                                    localStorage.setItem(
                                        "@greenmix:SELECTED_COMPOUND",
                                        JSON.stringify(e.target.value)
                                    )
                                }}
                                options={compounds}
                                optionLabel='name'
                                placeholder='Selecione um composto'
                            />
                        </div>
                        <div className='searchBar'>
                            <TextField id="outlined-basic" 
                            value={search}
                            label="Pesquisar" 
                            variant="outlined" 
                            size="small"
                            color="success"
                            onChange={(e) => setSearch(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='header--border'></div>
                </div>
                <div className='main'>
                    { isLoading ? 
                        <h3 id="loadingText">Carregando...</h3> 
                    :
                        (
                            <CompoundsPageContent 
                                compound={selectedCompound}
                                periodHooks={setHooks}
                            />
                        )
                    }
                </div>
                <div className="footer">
                    <ModalBase 
                        caller={
                        <div className='FAB addButton'>
                            <InsertDriveFileOutlinedIcon />
                        </div>
                        }
                        content={<ImportSheets />}
                        onClose={() => updateCompounds()}
                        title="Importar planilha"
                    />
                </div>
            </div>
        </div>
    )
}

export default CompoundsPage