import React, { useState } from 'react'

import api from '../../../../services/api'
import "./ImportSheets.css"

export default function ImportSheets(props) {
    const [file, setFile] = useState(undefined)
    const [status, setStatus] = useState()
    
    const handleSaving = () => {
        const postData = new FormData()
        postData.append("file", file)

        api.post('compounds/import', postData, {timeout: 0}).then(res => {
            setStatus(res.data)
        }).catch(err => alert("Ocorreu um erro ao processar a planilha, por favor tente novamente"))
    }

    const StatusReport = () => {
        const keys = Object.keys(status)

        return (
            <>
                {
                    keys?.map((key, index) => (
                        <div key={index} className='statusLine'>
                            <p className='statusLine--reference'>{key}: </p>
                            <div className='statusLine--description' 
                                style={{color: status[key].status == "error" ? "var(--app-salmon)" : "var(--app-green)"}}>
                                {
                                    status[key].status == "error" ?
                                        status[key].response?.map((err, index) => (
                                            <p key={index}>{err}</p>
                                        ))
                                        : <div>{status[key].response}</div>
                                }
                            </div>
                        </div>
                    ))
                }
            </>
        )
    }

    return(
        <div className='importSheets'>
            <div className="responseStatus">
                {file ? 
                    <>
                        Arquivo selecionado: {file.name}
                    </> 
                    : 
                    <>
                        Nenhum arquivo selecionado!
                    </>
                }
                <div className="statusContainer">
                    {status ?
                        <>
                           <StatusReport />
                        </>
                        :
                        <div style={{color: "rgba(255, 255, 255, 0.774)"}}>
                            <p>Aguardando processamento...</p>
                        </div>
                    }
                </div>
            </div>
            <div className='buttons'>
                <label className="selector" htmlFor='selectButton'>Selecionar um arquivo</label>
                <input id='selectButton'
                    type="file" 
                    accept='.csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel'
                    onChange={(e) => {
                        setFile(e.target.files[0])
                        setStatus()
                    }}
                />
                {file && !status ? 
                    <button className='uploadButton' onClick={handleSaving}>
                        Processar
                    </button> 
                    : 
                    <button className='uploadButton' onClick={() => props.closeModal()}>
                        Concluir
                    </button>
                }
            </div>
        </div>
    )
}