import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    id: null,
    username: null,
    unread_comments: null,
    can_access_greenmix: null,
    greenfilters_access: null,
    access_token: null,
    refresh_token: null,
    preferencies: {
        itemColumns: 6
    }
}

export const userSlice = createSlice({
    name: "user",
    initialState,
    reducers: {
        login: (state, action) => {
            state.id = action.payload.id
            state.username = action.payload.username
            state.unread_comments = action.payload.unread_comments
            state.access_token = action.payload.access_token
            state.refresh_token = action.payload.refresh_token
            state.can_access_greenmix = action.payload.can_access_greenmix
            state.greenfilters_access = action.payload.greenfilters_access
        },
        logout: (state, action) => {
            state.id = null
            state.username = null
            state.unread_comments = null
            state.access_token = null
            state.refresh_token = null
            state.can_access_greenmix = null
            state.greenfilters_access = null

            localStorage.removeItem("@greenMix:PERIOD_FILTERS")
            localStorage.removeItem("@greenMix:SELECTED_COMPOUND")
            localStorage.removeItem("@greenMix:TOKENS")
        },
        updateTokens: (state, action) => {
            state.access_token = action.payload.access
            state.refresh_token = action.payload.refresh
        },
        updateUnreadComments: (state, action) => {
            state.unread_comments = action.payload.comments
        },
        readComment: (state, action) => {
            let index = state.unread_comments.indexOf(action.payload.commentId);
            if (index !== -1) {
                state.unread_comments.splice(index, 1);
            }
        },
        changeItemColumns: (state, action) => {
            state.preferencies.itemColumns = action.payload.columns
        },
    }
})

export const { 
    login, logout, updateTokens, 
    readComment, updateUnreadComments, changeItemColumns
} = userSlice.actions;

export const selectUser = (state) => state.user;
export default userSlice.reducer;