import 'primeflex/primeflex.css';
import React, { useEffect, useState } from 'react';
import './style.css';

import { TextField } from '@mui/material';

import PhaseCard from '../../components/PhaseCard/PhaseCard';
import SideBar from '../../components/SideBar/SideBar';

import MobileSidebar from '../../components/MobileSidebar/MobileSidebar';
import usePhases, { usePhaseFilters } from '../../hooks/PhaseHandlers';
import { isAuthenticated } from '../../services/api';

const PhasesPage = () => {
    useEffect(() => {
        isAuthenticated()
    }, [])

    const {
        phases,
        isLoading
    } = usePhases()

    const {
        filters,
        filter
    } = usePhaseFilters()

    const [mobile, setMobile] = useState(false)
    const [search, setSearch] = useState(filters.reference || "");
    const [debouncedValue, setDebouncedValue] = useState("");

    useEffect(() => {
        const debounceTimer = setTimeout(() => {
            setDebouncedValue(search);
        }, 800);

        return () => {
            clearTimeout(debounceTimer);
        };
    }, [search]);

    const handleInputChange = (event) => {
        setSearch(event.target.value);
    };

    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth < 960) {
                setMobile(true)
            } else setMobile(false)
        }

        handleResize()
        window.addEventListener('resize', handleResize)

        return () => window.removeEventListener('resize', handleResize)
    }, [])

    useEffect(() => {
        if (debouncedValue || debouncedValue === "") {
            filter("reference", debouncedValue)
        }
    }, [debouncedValue])


    return (
        <div className='phasePage page'>
            {!mobile &&
                <SideBar
                    phaseFilters={filters}
                    filterPhases={filter}
                />
            }
            <div className='phasePage--body'>
                <div className='header'>
                    <div className='header--content'>
                        {mobile ?
                            <MobileSidebar
                                phaseFilters={filters}
                                filterPhases={filter}
                            />
                            :
                            <h2>Estes são os PROCESSOS</h2>
                        }
                        <div className='searchBar'>
                            <TextField id="outlined-basic"
                                value={search}
                                label="Pesquisar"
                                variant="outlined"
                                size="small"
                                color="success"
                                onChange={handleInputChange}
                            />
                        </div>
                    </div>
                    <div className='header--border'></div>
                </div>
                <div className='main'>
                    {isLoading ?
                        <h3 id="loadingText">Carregando...</h3>
                        :
                        (
                            phases?.map((phase) => (
                                <div key={phase.phase + phase.sequence} className='phaseContainer'>
                                    <PhaseCard
                                        title={phase.phase}
                                        data={phase.products}
                                        phase={phase}
                                        allPhases={phases}
                                        filters={filters}
                                    />
                                </div>)
                            )
                        )
                    }
                </div>
            </div>
        </div>
    )
}

export default PhasesPage