import { Modal } from "@mui/material";
import { useState } from "react";

import { useQuery } from "react-query";
import api from "../../../../services/api";
import { AddFilterModal } from "../AddFilterModal/AddFilterModal";
import { InlineFilter } from "./InlineFilter/InlineFilter.jsx";
import "./ViewFiltersModal.css";


export const ViewFiltersModal = () => {
    const [open, setOpen] = useState(false)
    const { data: filters } = useQuery(
        ["filters"], 
        () => api.get("filters").then(res => res.data)
    )

    const handleClose = () => {
        setOpen(false)
    }

    return (
        <>
            <button onClick={() => setOpen(true)}>Ver filtros</button>
            { open &&
                <Modal open={open} onClose={handleClose}>
                    <div id="vf-md-ct">
                        <div id="vf-md">
                            <div className="heading">
                                <div style={{display: "flex", alignItems: "center", gap: "1rem"}}>
                                    <h3>Ver filtros</h3>
                                    <AddFilterModal />
                                </div>
                                <i className="pi pi-times" onClick={() => handleClose()}/>
                            </div>
                            <div className="filters">
                                {filters &&
                                    filters.map(filter => <InlineFilter data={filter} key={filter.id}/>)
                                }
                            </div>
                        </div>
                    </div>
                </Modal>
            }
        </>
    )
}