/* eslint-disable react-hooks/exhaustive-deps */
import { ButtonBase } from "@mui/material";
import { Dropdown } from "primereact/dropdown";
import React, { useRef, useState } from "react";
import DraftEstimative from '../DraftEstimative/DraftEstimative';

import { Button } from 'primereact/button';
import { Toast } from 'primereact/toast';

import {
	useMaterials,
} from "../../../../hooks/EstimativeHandlers";
import "./AddMaterialEstimative.css";

export default function AddMaterialEstimative(props) {
	const [details, setDetails] = useState({
		material: "",
		color: "",
	});
	const [drafts, setDrafts] = useState([])
	const [count, setCount] = useState(0)
	const { materials } = useMaterials();
	const toast = useRef()
	props.backdropAutoClose(false);
	
	const draftData = () => {
		let data = {
			key: count,
			id: count,
			removeDraft: () => removeDraft(count),
			data: {}
		}

		setCount(c => c += 1)

		return data
	}

	const setDraftData = (id, data) => {
        setDrafts(state => state.map(d => {
            if (d.id === id) {
                d.data = data
            }

            return d
        }))
    }

    const saveDrafts = () => {
		let totalAssortment = 0

        drafts.map(draft => totalAssortment += draft.data.assortment)

        if (totalAssortment === 100) {
            drafts.map(draft => props.addEstimative({...draft.data, color: draft.data.color.id}))
        	props.closeModal()
        } else {
            toast.current.show({
                severity: 'error', 
                summary: 'Sortimento inválido', 
                detail: 'A soma dos sortimentos deve equivaler a 100%',
                life: 4000
            });
        }
    }

	const removeDraft = (index) => {
        setDrafts(d => [...d].filter(draft => Number(draft.key) !== index))
    }

	const SelectedMaterialTemplate = (option, props) => {
		if (option) {
			return (
				<div className="material-item">
					<p>{option.code} / <span className="materialDescription">{option.description}</span></p>
				</div>
			);
		}

		return (
			<span>
				{props.placeholder}
			</span>
		);
	}

	const MaterialOptions = (option) => {
		return (
			<div className="material-item">
				<p>{option.code} / <span className="materialDescription">{option.description}</span></p>
			</div>
		);
	}


	return (
		<div className="AddMaterialEstimative">
			<Toast ref={toast} />
			<div className="material-selector">
				<p>Material</p>
				<Dropdown
					value={details.material}
					onChange={(e) => {
						setDetails({ ...details, material: e.value });
					}}
					filter
					options={materials.filter(
						mat => !props.currentEstimatives.map(est => est.material.id).includes(mat.id)
					)}
					optionLabel="code"
					placeholder="Selecione um material"
					itemTemplate={MaterialOptions}
					valueTemplate={SelectedMaterialTemplate}
					filterBy="code,description"
				/>
			</div>
			{details.material && drafts.length > 0 &&
				<table className='mc-colors'>
					<thead>
						<tr>
							<th>Cor</th>
							<th>Consumo</th>
							<th>Sortimento</th>
						</tr>
					</thead>
					<tbody>
						{details.material &&
							drafts.map((d) => (
								<DraftEstimative 
									key={d.key} 
									id={d.id}
									matId={details.material.id} 
									prodId={props.prodId}
									removeDraft={d.removeDraft}
									setDraftData={setDraftData}
									currentColors={[]}
								/>
							))
						}
					</tbody>
				</table>
			}
			{details.material &&
				<Button id="add-color-material"
					className="p-button-rounded p-button-success"
					onClick={() => setDrafts(d => [...d, draftData()])}
				>
					<i className='pi pi-plus' />
				</Button>
			}
			{details.material && drafts.length > 0 &&
				<div>
					<ButtonBase onClick={() => saveDrafts()} className="greenButton">
						Salvar
					</ButtonBase>
				</div>
			}
		</div>
	);
}