import React, { useEffect, useState } from 'react';

import useCompounds, { useCompoundItem } from '../../../../hooks/CompoundHandlers';

import useProps from "../../../../hooks/PropsHandlers";
import { ProductCard } from '../../../ProductCard/ProductCard';
import EditSaveButton from '../../../shared/EditSaveButton/EditSaveButton';
import ModalBase from '../../../shared/ModalBase/ModalBase';
import AddProduct from '../../Products/AddProduct/AddProduct';
import ViewProduct from '../../Products/ViewProduct/ViewProduct';
import ListDemands from '../ListDemands/ListDemands';

import { ButtonBase } from '@mui/material';
import { Checkbox } from 'primereact/checkbox';
import { confirmDialog } from 'primereact/confirmdialog';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';

import ViewLogCompound from "../ViewLogCompound/ViewLogCompound";

import AsyncSelect from 'react-select/async';
import { toast } from 'react-toastify';
import { useSWRConfig } from "swr";
import api from '../../../../services/api';
import { CompoundProductComments } from './ProductComments/CompoundProductComments';
import './ViewProductCompound.css';

export default function ViewProductCompound(props) {
    const [image, setImage] = useState(props.data ? props.data.image : null);
    const [editing, setEditing] = useState(false)
    const { compounds, updateCompounds } = useCompounds()
    const { mutate } = useSWRConfig()
    const { 
        item, deleteItem, patchItem, 
        patchImage
    } = useCompoundItem(props.data ? props.data.id : props.itemId)
    const [details, setDetails] = useState(props.data ? {
        product: props.data.product,
        observations: props.data.observations,
        period: props.period,
        typed: props.data.typed
    } : null)
    const [selected, setSelected] = useState({})

    const {
		designers
	} = useProps();

    const resetData = () => {
        setDetails(item)
        setImage(item.image)
    }

    const handleEdit = () => {
        setEditing(true)
    }

    useEffect(() => {
        props.backdropAutoClose(!editing)
    }, [editing])

    useEffect(() => {
        if (compounds && item) {
            setSelected(compounds.find(compound => compound.periods.some(period => period.id === item.period)))
        }
    }, [compounds, item])

    useEffect(() => {
        if (item) {
            resetData()
        }
    }, [item])
    
    const handleSave = async () => {
        if (details?.product && details?.period) {
            setEditing(false)
            let res = await patchItem(props.data ? props.data.id : props.itemId, details)

            if (res.status === 400) {
                if (res.data[0].split(":")[0] === "GNR001") {
                    toast.error("O item já existe no período de destino!")
                }
                return resetData()
            }

            if (res.status === 403) {
                return resetData()
            }

            if (details?.product.generic && image && !(typeof(image) === "string") && image != "") {
                patchImage(props.data.id, image)
            }

            mutate(`compounds/items`)
        } else {
            alert("Por favor verifique os dados inseridos")
        }
    } 

    const confirmRemove = () => {
        confirmDialog({
            message: `Remover item?`,
            header: 'Remover item composto',
            icon: 'pi pi-exclamation-triangle',
            acceptLabel: 'Sim',
            rejectLabel: 'Não',
            accept: () => {
                deleteItem(props.data.id, () => {
                    props.closeModal?.()
                    props.updateItems?.()
                })
            },
            reject: () => {}
        });
    }


    const handleCancel = () => {
        resetData()
        setEditing(false) 
    }

    const loadOptions = (inputValue, callback) => {
        api.get(
            `products/dropdown?limit=15&search=${inputValue}`
        ).then(res => callback(res.data.results))
    };

    return (
        <div className='viewProductCompound'>
            <div className="p-grid">
                <div className="p-col">
                    <div className='productContainer'>
                        {item && details &&
                            <ModalBase
                                caller={<ProductCard 
                                    prodId={details?.product.id}
                                    doubleSize 
                                    genericImg={
                                        details?.product.generic ? 
                                        props?.genericImg : undefined
                                    }
                                    key={details?.product.reference}
                                />}
                                content={<ViewProduct productId={details?.product.id} />}
                                title={details?.product.reference}
                            />
                        }
                    </div>
                </div>
                <div className="p-col">
                    <div className='detailsContainer p-grid p-dir-col'>
                        <div className="pcol">
                            <div className="p-grid p-dir-col">
                                <div className="p-col details--product">
                                    <h4>Produto</h4>
                                    <AsyncSelect onChange={(selected) => setDetails({ ...details, product: selected })}
                                        styles={{
                                            container: (baseStyles, state) => ({
                                              ...baseStyles,
                                              width: "100%",
                                              height: "2.5rem"
                                            }),
                                            option: (baseStyles, state) => ({
                                                ...baseStyles,
                                                color: "black",
                                              }),
                                            }}
                                        cacheOptions
                                        loadOptions={loadOptions}
                                        isDisabled={!editing}
                                        defaultOptions
                                        placeholder={details?.product.reference || "Selecione um produto"}
                                        getOptionLabel={(obj) => obj.reference}
                                        getOptionValue={(obj) => obj.id}
                                    />
                                    {editing ? 
                                    <>
                                        <p>ou</p>
                                        <ModalBase
                                            caller={<div className='details--createProduct'>Criar novo produto</div>}
                                            content={<AddProduct />}
                                            title="Adicionar PRODUTO ao COMPOSTO"
                                        />
                                    </> : <></>
                                    }
                                </div>
                                <div className="p-col">
                                    <h4>Período</h4>
                                    <Dropdown value={details?.period}
                                        onChange={(e) => setDetails({...details, period: e.target.value})}
                                        disabled={!editing}
                                        options={selected.periods}
                                        optionLabel='name' optionValue='id'
                                        placeholder='Selecione um período'
                                    />
                                </div>
                                <div className="p-col">
                                    <h4>Designer</h4>
                                    <Dropdown value={details?.generic_designer}
                                        onChange={(e) => setDetails({...details, generic_designer: e.target.value})}
                                        disabled={!editing}
                                        options={designers}
                                        optionLabel='name' optionValue='id'
                                        placeholder='Selecione um designer genérico'
                                    />
                                </div>
                                <div className="p-col">
                                    <h4>Observações</h4>
                                    <InputText value={details?.observations} 
                                        onChange={(e) => setDetails({...details, observations: e.target.value})}
                                        disabled={!editing}
                                    />
                                </div>
                                <div className="p-col" style={{display: "inline-flex", gap: "0.25rem", marginBottom: "0.5rem"}}>
                                    <Checkbox onChange={
                                        e => setDetails({...details, typed: e.checked})
                                    } 
                                    checked={details?.typed} 
                                    disabled={!editing}/>
                                    <p>Pedido digitado</p>
                                </div>
                                { !editing && details?.product.generic ?
                                <div className="p-col">
                                    <ModalBase
                                        caller={
                                            <div className='greenBorderButton'>
                                                Ver Imagem genérica
                                            </div>
                                        }
                                        content={
                                            <img src={item?.image} alt={`imagem de ${details?.reference}`} />
                                        }
                                        title="Visualizar imagem"
                                    />
                                </div> : null
                                }
                                { editing && details?.product.generic ?
                                <div className="p-col">
                                    <label className="salmonBorderButton" htmlFor='selectButton'>
                                        <p className='imageText'>imagem genérica</p>
                                        <input id='selectButton'
                                            type="file" accept='image/*'
                                            onChange={(e) => {
                                                setImage(e.target.files[0])
                                            }}
                                        />
                                    </label>
                                </div> : null
                                }
                                { !editing &&
                                <div className="p-col" style={{display: "flex", alignItems: "center", gap: "1rem"}}>
                                    {details &&
                                        <ModalBase 
                                            caller={
                                            <div className='greenBorderButton'>
                                                Demandas
                                            </div>
                                            }
                                            content={<ListDemands 
                                                    item={props.data ? props.data : details} 
                                                    onClose={() => {
                                                        updateCompounds()
                                                    }}
                                                    period={props.period ? props.period : details.period}
                                                />
                                            }
                                            title="Visualizar demandas"
                                        />
                                    }
                                    <ModalBase
                                        title="Histórico do PRODUTO no COMPOSTO"
                                        caller={
                                            <div className='yellowBorderButton'>
                                            <p>Histórico</p>
                                        </div>
                                        }
                                        content={<ViewLogCompound compoundId={props.data ? props.data?.id : props.itemId}/>}
                                    />
                                </div>
                                }
                            </div>
                        </div>
                        <div className="pcol buttons">
                            <EditSaveButton onEdit={handleEdit} onSave={handleSave} onCancel={handleCancel}/>
                            { !editing ?
                            <ButtonBase className='redButton' onClick={confirmRemove}>
                                Remover item
                            </ButtonBase> : null
                            }
                        </div>
                    </div>
                </div>
            </div>
            { (props.data?.id || props.itemId) &&
                <CompoundProductComments itemId={props.data ? props.data?.id : props.itemId}/>
            }
        </div>
    )
}