import React, { useEffect, useState } from 'react';
import './styles.css';

import { ButtonBase } from '@mui/material';
import { InputText } from 'primereact/inputtext';

import { useNavigate } from "react-router-dom";
import api from '../../services/api';

import { useDispatch } from 'react-redux';
import { login } from '../../app/slices/userSlice';

const LoginPage = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()

    useEffect(() => {
        if (localStorage.getItem("@greenMix:TOKENS")) {
            navigate(`/products`, {replace: true})
        } else {
            localStorage.removeItem("@greenmix:PERIOD_FILTERS")
            localStorage.removeItem("@greenMix:SELECTED_COMPOUND")
        }
    }, [])

    const [user, setUser] = useState({
        username: "",
        password: ""
    })

    function parseJwt (token) {
        var base64Url = token.split('.')[1];
        var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
        var jsonPayload = decodeURIComponent(window.atob(base64).split('').map(function(c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
        }).join(''));

        return JSON.parse(jsonPayload);
    };

    const signIn = () => {
        api.post('/token/', user).then(res => {
            let data = parseJwt(res.data.access)
            dispatch(login({
                id: data.user_id,
                username: res.data.username,
                unread_comments: res.data.unread_comments,
                access_token: res.data.access,
                refresh_token: res.data.refresh,
                can_access_greenmix: res.data.has_greenmix_access,
                greenfilters_access: res.data.greenfilter_access,
            }))

            if (res.status === 200) {
                localStorage.setItem("@greenMix:TOKENS", JSON.stringify(res.data))
                if (res.data.has_greenmix_access) {
                    navigate(`/products`, {replace: true})
                } else {
                    navigate(`/filters`, {replace: true})
                }
            }
        }).catch(err => {
            console.log(err)
            alert("Usuário ou senha inválidos")
        })
    }

    return (
        <div className='loginPage'>
            <div className='upper'>
                <div className='content'>
                    <img src="/assets/images/GreenMix.svg" alt="logo greenmix"/>
                    <InputText 
                        value={user.username} 
                        onChange={(e) => setUser({...user, username: e.target.value})}
                        placeholder="Nome de usuário"
                    />
                    <InputText 
                        value={user.password} 
                        onChange={(e) => setUser({...user, password: e.target.value})}
                        placeholder="Senha"
                        type="password"
                    />
                    <ButtonBase 
                        className='greenButton'
                        onClick={() => signIn()}
                    >Entrar</ButtonBase>
                </div>
            </div>
            <div className='lower'>
                <img id="login--img" src="/assets/images/Login_illustration.svg" alt="ilustração de login"/>
            </div>
        </div>
    )
}

export default LoginPage