import axios from "axios";
import { logout, updateTokens, updateUnreadComments } from "../app/slices/userSlice";
import store from "../app/store";

export const URL_ACCESS = 'https://greenmixapi.azurewebsites.net'
// export const URL_ACCESS = 'http://localhost:8000'


const api = axios.create({
	baseURL: `${URL_ACCESS}/api`,
	headers: {
		'Content-Type': 'application/json; charset=utf-8',
		'Accept': 'application/json, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
	},
	timeout: 60 * 1000, // 60 segundos (em ms)
	transformRequest: [(data, headers) => {
		const tokens = JSON.parse(localStorage.getItem('@greenMix:TOKENS'))
		const accessToken = tokens?.access

		if (accessToken) {
			headers['Authorization'] = `Bearer ${accessToken}`
		}

		if (data instanceof FormData) {
			return data
		} else {
			return JSON.stringify(data);
		}
	}],
})

api.interceptors.response.use((response) => response, (error) => {
	console.log(error.code)
	if (error.response.status === 403) {
		alert("Você não tem permissão para executar esta ação.")
	}

	throw error;
});

export const isAuthenticated = () => {
	const tokens = JSON.parse(localStorage.getItem('@greenMix:TOKENS'))
	const accessToken = tokens?.access

	if (accessToken) {
		api.post("token/verify/", { token: accessToken }).then((res) => {
			if (res.status === 200) {
				silentRefresh(tokens)
			}
			store.dispatch(updateUnreadComments({comments: res.data.unread_comments}))
		}).catch(() => {
			window.location.assign('/');
			localStorage.removeItem('@greenMix:TOKENS')
			store.dispatch(logout())
		})
	} else {
		window.location.assign('/');
		localStorage.removeItem('@greenMix:TOKENS')
		store.dispatch(logout())
	}
}

const silentRefresh = (tokens) => {
	api.post("token/refresh/", { refresh: tokens.refresh }).then(res => {
		if (res.status === 200) {
			let updatedTokens = { ...tokens, access: res.data.access }
			store.dispatch(updateTokens(updatedTokens))
			localStorage.setItem('@greenMix:TOKENS', JSON.stringify(updatedTokens))
		}
	})
}

export default api