import { Modal } from "@mui/material";
import { Dropdown } from "primereact/dropdown";
import { useState } from "react";

import { useMutation, useQuery, useQueryClient } from "react-query";
import { toast } from 'react-toastify';
import api from "../../../../services/api";
import "./AddParamModal.css";


export const AddParamModal = (props) => {
    const [open, setOpen] = useState(false)
    const [details, setDetails] = useState({
        store: props.store,
        filter: null,
    })
    const client = useQueryClient()
    const { data: filters } = useQuery(["filters", "available", props.store], () => api.get("filters?available_in_store=" + props.store).then(res => res.data))

    const saveParam = useMutation(() => api.post("filters/store/" + props.store, details).then(() => {
        client.invalidateQueries(["params", "store", props.store])
        client.invalidateQueries(["filters", "available"])
        toast.success("Parâmetro adicionado com sucesso!")
        setDetails({
            filter: null,
        })
        handleClose()
    }).catch(() => toast.error("Não foi possível criar o parâmetro!"))
)

    const handleClose = () => {
        setOpen(false)
        setDetails({
            filter: null,
        })
    }

    const handleSave = (e) => {
        e.preventDefault()
        saveParam.mutate()
    }

    return (
        <>
            <i onClick={() => setOpen(true)} className="addp-fab pi pi-plus" />
            { open &&
                <Modal open={open} onClose={handleClose}>
                    <div id="addp-md-ct">
                        <div id="addp-md">
                            <div className="heading">
                                <h3>Adicionar parâmetro</h3>
                                <i className="pi pi-times" onClick={() => handleClose()}/>
                            </div>
                            <form className="content">
                                <Dropdown value={details.filter} 
                                    onChange={(e) => setDetails({...details, filter: e.target.value})}
                                    options={filters}
                                    filter
                                    optionLabel='name'
                                    optionValue="id"
                                    placeholder='Selecione o filtro'
                                />
                                <button onClick={handleSave}>Salvar</button>
                            </form>
                        </div>
                    </div>
                </Modal>
            }
        </>
    )
}