import { ButtonBase } from "@mui/material";
import Modal from "@mui/material/Modal";
import { confirmDialog } from "primereact/confirmdialog";
import React, { forwardRef, useState } from "react";
import "./ModalBase.css";
import { ProductComments } from "./ProductComments/ProductComments";

const ModalBase = forwardRef((props, ref) => {
	const [open, setOpen] = useState(false);
	const handleOpen = () => setOpen(true);
	let backdropClose = true;

	const changeBackgroupClose = (value) => {
		backdropClose = value;
	};

	const handleClose = (event, reason) => {
		if (reason === "backdropClick" && !backdropClose) {
			confirmClosing();
		} else {
			if (props?.onClose) props.onClose();
			setOpen(false);
		}
	}

	const confirmClosing = () => {
		confirmDialog({
			message:
				"Tem certeza que deseja fechar o modal? Alterações não salvas serão perdidas",
			header: "Atenção",
			icon: "pi pi-exclamation-triangle",
			acceptLabel: "Sim",
			rejectLabel: "Não",
			accept: handleClose,
			reject: () => { },
		});
	};

	const Content = () => {
		return React.cloneElement(props.content, {
			closeModal: handleClose,
			backdropAutoClose: changeBackgroupClose,
		});
	};

	return (
		<>
			<ButtonBase
				style={{ display: "flex", flexDirection: "column", justifyContent: "start" }}
				onClick={handleOpen}
				id={props.buttonId ? props.buttonId : ""}
				ref={ref}
			>
				{props.caller}
			</ButtonBase>
			<Modal
				open={open}
				onClose={handleClose}
				aria-labelledby="modal-modal-title"
				aria-describedby="modal-modal-description"
			>
				<div className="container">
					<div className="modal-header">
						<h3>{props.title}</h3>
						<div>
							{props.commentsIcon &&
								<ProductComments commentsUrl={props.commentsUrl} />
							}
							<ButtonBase onClick={handleClose} className="closeButton">
								X
							</ButtonBase>
						</div>
					</div>
					<div className="modal-content">
						<Content />
					</div>
				</div>
			</Modal>
		</>
	);
})

export default ModalBase