import { InputSwitch } from "primereact/inputswitch";
import { MultiSelect } from 'primereact/multiselect';
import { TriStateCheckbox } from 'primereact/tristatecheckbox';
import React, { memo } from 'react';

import { useQuery } from 'react-query';
import useCompounds from '../../hooks/CompoundHandlers';
import { useMaterials } from '../../hooks/EstimativeHandlers';
import useProps from '../../hooks/PropsHandlers';
import api from '../../services/api';

function ProductFilters(props) {
    const { 
        lines, 
        groups, 
        collections,
        stylists,
        designers
    } = useProps()
    const { materials } = useMaterials()
    const { compounds } = useCompounds()
    const filterProducts = props.filterProducts
    const productFilters = props.productFilters

    const { data: productTags } = useQuery(
        "productTags", 
        () => api.get("tags/types?type=1").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )
    const { data: processTags } = useQuery(
        "processTags", 
        () => api.get("tags/types?type=2").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )

    return (
        <>
            <div style={{marginBottom: "0.5rem"}}>
                <p style={{marginBottom: "0.25rem"}}>Filtragem de tags</p>
                <div style={{display: "flex", gap: "0.5rem"}}>
                    <p>Uma</p>
                    <InputSwitch checked={productFilters.filter_type === "AND"} onChange={(e) => {
                        filterProducts("filter_type", productFilters.filter_type === "OR" ? "AND" : "OR")
                    }} />
                    <p>Todas</p>
                </div>
            </div>
            <MultiSelect optionLabel="name" value={productFilters.product_tags} options={productTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filterProducts("product_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de produto"
            />
            <MultiSelect optionLabel="name" value={productFilters.process_tags} options={processTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filterProducts("process_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de processo"
            />
            <MultiSelect optionLabel="name" optionValue="id" 
                value={productFilters.line} options={lines} 
                filter display='chip'
                onChange={(e) => {
                    filterProducts("line", e.value === undefined ? "" : e.value)
                }}
                placeholder="Linhas"
            />
            <MultiSelect optionLabel="name" optionValue="id" 
                value={productFilters.group} options={groups} 
                filter display='chip'
                onChange={(e) => {
                    filterProducts("group", e.value === undefined ? "" : e.value)
                }}
                placeholder="Grupos"
            />
            {productFilters.group && productFilters.length !== 0 &&
                <MultiSelect 
                    optionGroupLabel="name" optionGroupChildren="subgroups"
                    optionLabel="name" optionValue="id" 
                    value={productFilters.subgroup} 
                    options={
                        groups?.filter(group => productFilters.group.includes(group.id))
                    } 
                    filter display='chip'
                    onChange={(e) => {
                        filterProducts("subgroup", e.value === undefined ? "" : e.value)
                    }}
                    placeholder="Subgrupos"
                />
            }
            <MultiSelect optionLabel="name" optionValue="id" 
                value={productFilters.collection} options={collections} 
                filter display='chip'
                onChange={(e) => {
                    filterProducts("collection", e.value === undefined ? "" : e.value)
                }}
                placeholder="Coleções"
            />
            <MultiSelect optionLabel="name" optionValue="id" 
                value={productFilters.compound} options={compounds} 
                filter display='chip'
                onChange={(e) => {
                    filterProducts("compound", e.value === undefined ? "" : e.value)
                }}
                placeholder="Compostos"
            />
            {productFilters.compound && productFilters.compound !== 0 &&
                <MultiSelect 
                    optionGroupLabel="name" optionGroupChildren="periods"
                    optionLabel="name" optionValue="id" 
                    value={productFilters.period} 
                    options={
                        compounds?.filter(compound => productFilters.compound.includes(compound.id))
                    } 
                    filter display='chip'
                    onChange={(e) => {
                        filterProducts("period", e.value === undefined ? "" : e.value)
                    }}
                    placeholder="Periodos"
                />
            }
            <MultiSelect optionLabel="code" optionValue="id"
                value={productFilters.material} options={materials}
                filter display='chip'
                onChange={(e) => {
                    filterProducts("material", e.value === undefined ? "" : e.value)
                }}
                placeholder="Materiais"
            />
            <MultiSelect optionLabel="name" optionValue="id"
                value={productFilters.stylist} options={stylists}
                filter display='chip'
                onChange={(e) => {
                    filterProducts("stylist", e.value === undefined ? "" : e.value)
                }}
                placeholder="Estilistas"
            />
            <MultiSelect optionLabel="name" optionValue="id"
                value={productFilters.designer} options={designers}
                filter display='chip'
                onChange={(e) => {
                    filterProducts("designer", e.value === undefined ? "" : e.value)
                }}
                placeholder="Designers"
            />
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb2" 
                    onChange={e => filterProducts("erp", e.value)} 
                    value={productFilters?.erp} 
                />
                <label htmlFor="cb2" className="p-checkbox-label">Cadastrado ERP</label>
            </div>
            <div className='checkfilter bottom'>
                <TriStateCheckbox 
                    inputId="cb3" 
                    onChange={e => filterProducts("generic", e.value)} 
                    value={productFilters?.generic} 
                />
                <label htmlFor="cb3" className="p-checkbox-label">Produto genérico</label>
            </div>
        </>
    )
}

export default memo(ProductFilters)