import { confirmDialog } from "primereact/confirmdialog"
import { forwardRef } from "react"
import { useMutation, useQueryClient } from "react-query"
import { useDispatch, useSelector } from "react-redux"
import { readComment, selectUser } from "../../../app/slices/userSlice"
import ViewProductCompound from "../../../components/Modals/Compounds/ViewProductCompound/ViewProductCompound"
import ViewProduct from "../../../components/Modals/Products/ViewProduct/ViewProduct"
import ModalBase from "../../../components/shared/ModalBase/ModalBase"
import api from "../../../services/api"
import "./InlineComment.css"

export const InlineComment = forwardRef((props, ref) => {
    const user = useSelector(selectUser)
    const comment = props.comment
    const date = new Date(comment.created_at)
    const isAuthor = comment.user === user.username
    const client = useQueryClient()
    const unread = user.unread_comments.includes(comment.id)
    const dispatch = useDispatch()

    const SubjectCaller = () => {
        if (props.noCaller) return comment.subject

        return (
            <ModalBase
                caller={<>{comment.subject}</>}
                content={comment.type === "product" ? <ViewProduct productId={comment.subject_id} /> : <ViewProductCompound itemId={comment.subject_id} />}
                title={comment.subject}
                commentsIcon={comment.type === "product" ? true : undefined}
                commentsUrl={comment.type === "product" ? `comments?product=${comment.subject_id}` : undefined}
            />
        )
    }

    const deleteComment = useMutation(
        () => api.delete("comments/" + comment.id).then(res => res.data),
        {
            onSuccess: () => {
                if (props.onDeleteInvalidateKey) {
                    client.invalidateQueries(props.onDeleteInvalidateKey)
                } else {
                    client.invalidateQueries("comments")
                    client.invalidateQueries("infiniteCompoundComments")
                    client.invalidateQueries("infinitePhaseComments")
                }
            }
        }
    )

    const confirmClosing = () => {
        confirmDialog({
            message:
                "Tem certeza que deseja deletar esse comentário?",
            header: "Atenção",
            icon: "pi pi-exclamation-triangle",
            acceptLabel: "Sim",
            rejectLabel: "Não",
            accept: () => deleteComment.mutate(),
            reject: () => {},
        });
    };

    const markAsRead = () => {
        api.post("comments/" + comment.id + "/read").then(
            res => {
                if (res.status === 204) {
                    dispatch(readComment({commentId: comment.id}))
                }
            }
        )
    }

    return (
        <div className={`comment-ct ${unread ? "unread" : ""}`} ref={ref}>
            <div className="com-header">
                <p><b>{comment.user}</b> sobre <b><SubjectCaller /></b></p>
                <div style={{ display: "flex", gap: "0.65rem" }}>
                    <p>{date.toLocaleString("pt-BR")}</p>
                    {isAuthor &&
                        <div className="comment-act">
                            <i className="pi pi-trash" onClick={() => confirmClosing()} />
                        </div>
                    }
                    {unread &&
                        <div className="comment-act">
                            <i className="pi pi-check-circle" 
                                onClick={() => markAsRead()} 
                            />
                        </div>
                    }
                </div>
            </div>
            <div className="com-body">
                <p>{comment.content}</p>
            </div>
        </div>
    )
})