import React, { memo } from 'react';

import { MultiSelect } from 'primereact/multiselect';
import { Dropdown } from 'primereact/dropdown';
import { TriStateCheckbox } from 'primereact/tristatecheckbox';

import { useQuery } from 'react-query';
import useCompounds from '../../hooks/CompoundHandlers';
import { useMaterials } from '../../hooks/EstimativeHandlers';
import useProps from '../../hooks/PropsHandlers';
import api from '../../services/api';

function PhaseFilters(props) {
    const { 
        lines, 
        groups, 
        collections,
        stylists,
        designers
    } = useProps()
    const { compounds } = useCompounds()
    const { materials } = useMaterials()

    const { data: productTags } = useQuery(
        "productTags", 
        () => api.get("tags/types?type=1").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )
    const { data: processTags } = useQuery(
        "processTags", 
        () => api.get("tags/types?type=2").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )

    const filterPhases = props.filterPhases
    const filters = props.phaseFilters

    const sortOptions = [
        {
            "id": "reference",
            "name": "Referência (cres)"
        },
        {
            "id": "-reference",
            "name": "Referência (dec)"
        },
        {
            "id": "-added_on_phase",
            "name": "Dias na fase (cres)"
        },
        {
            "id": "added_on_phase",
            "name": "Dias na fase (dec)"
        },
        {
            "id": "limit_date",
            "name": "Data limite (cres)"
        },
        {
            "id": "-limit_date",
            "name": "Data limite (dec)"
        },
    ]

    return ( 
        <>
            <Dropdown value={filters.ordering} options={sortOptions}
                onChange={(e) => {
                    filterPhases("ordering", e.value === undefined ? "" : e.value)
                }}
                optionValue='id' optionLabel='name'
                placeholder="Ordenação"
            />
            <MultiSelect optionLabel="name" value={filters.product_tags} options={productTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filterPhases("product_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de produto"
            />
            <MultiSelect optionLabel="name" value={filters.process_tags} options={processTags} filter
                optionGroupLabel="name" optionGroupChildren="tags" display='chip'
                onChange={(e) => {
                    filterPhases("process_tags", e.value === undefined ? "" : e.value)
                }}
                placeholder="Tags de processo"
            />
            <MultiSelect optionLabel="name" optionValue="id" 
                value={filters.line} options={lines} 
                filter display='chip'
                onChange={(e) => {
                    filterPhases("line", e.value === undefined ? "" : e.value)
                }}
                placeholder="Linhas"
            />
           <MultiSelect optionLabel="name" optionValue="id" 
                value={filters.group} options={groups} 
                filter display='chip'
                onChange={(e) => {
                    filterPhases("group", e.value === undefined ? "" : e.value)
                }}
                placeholder="Grupos"
            />
            {filters.group && filters.length !== 0 &&
                <MultiSelect 
                    optionGroupLabel="name" optionGroupChildren="subgroups"
                    optionLabel="name" optionValue="id" 
                    value={filters.subgroup} 
                    options={
                        groups?.filter(group => filters.group.includes(group.id))
                    } 
                    filter display='chip'
                    onChange={(e) => {
                        filterPhases("subgroup", e.value === undefined ? "" : e.value)
                    }}
                    placeholder="Subgrupos"
                />
            }
            <MultiSelect optionLabel="name" optionValue="id" 
                value={filters.collection} options={collections} 
                filter display='chip'
                onChange={(e) => {
                    filterPhases("collection", e.value === undefined ? "" : e.value)
                }}
                placeholder="Coleções"
            />
            <MultiSelect optionLabel="name" optionValue="id" 
                value={filters.compound} options={compounds} 
                filter display='chip'
                onChange={(e) => {
                    filterPhases("compound", e.value === undefined ? "" : e.value)
                }}
                placeholder="Compostos"
            />
            {filters.compound && filters.compound !== 0 &&
                <MultiSelect 
                    optionGroupLabel="name" optionGroupChildren="periods"
                    optionLabel="name" optionValue="id" 
                    value={filters.period} 
                    options={
                        compounds?.filter(compound => filters.compound.includes(compound.id))
                    } 
                    filter display='chip'
                    onChange={(e) => {
                        filterPhases("period", e.value === undefined ? "" : e.value)
                    }}
                    placeholder="Periodos"
                />
            }
            <MultiSelect optionLabel="code" optionValue="id" 
                value={filters.material} options={materials} 
                filter display='chip'
                onChange={(e) => {
                    filterPhases("material", e.value === undefined ? "" : e.value)
                }}
                placeholder="Materiais"
            />
            <MultiSelect optionLabel="name" optionValue="id"
                value={filters.stylist} options={stylists}
                filter display='chip'
                onChange={(e) => {
                    filterPhases("stylist", e.value === undefined ? "" : e.value)
                }}
                placeholder="Estilistas"
            />
            <MultiSelect optionLabel="name" optionValue="id"
                value={filters.designer} options={designers}
                filter display='chip'
                onChange={(e) => {
                    filterPhases("designer", e.value === undefined ? "" : e.value)
                }}
                placeholder="Designers"
            />
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb2" 
                    onChange={e => filterPhases("erp", e.value)} 
                    value={filters?.erp} 
                />
                <label htmlFor="cb2" className="p-checkbox-label">Cadastrado ERP</label>
            </div>
            <div className='checkfilter'>
                <TriStateCheckbox 
                    inputId="cb3" 
                    onChange={e => filterPhases("showcase", e.value)} 
                    value={filters?.showcase} 
                />
                <label htmlFor="cb3" className="p-checkbox-label">Mostruário</label>
            </div>
            <div className='checkfilter bottom'>
                <TriStateCheckbox 
                    inputId="cb4" 
                    onChange={e => filterPhases("generic", e.value)} 
                    value={filters?.generic} 
                />
                <label htmlFor="cb4" className="p-checkbox-label">Produto genérico</label>
            </div>
        </>
    )
}

export default memo(PhaseFilters)