import { BrowserRouter, Navigate, Route, Routes, useLocation } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import './App.css';

import CompoundsPage from './pages/CompoundsPage';
import LoginPage from './pages/LoginPage';
import PhasesPage from './pages/PhasesPage';
import ProductsPage from './pages/ProductsPage';

import 'react-toastify/dist/ReactToastify.css';
import { CommentsPage } from './pages/CommentsPage';
import { GreenFiltersPage } from './pages/GreenFiltersPage/GreenFiltersPage';

function RequireAuth({ children }) {
	const tokens = JSON.parse(localStorage.getItem('@greenMix:TOKENS'))
	const accessToken = tokens?.access
	let location = useLocation()

	if (!accessToken) {
		// Redirect them to the /login page, but save the current location they were
		// trying to go to when they were redirected. This allows us to send them
		// along to that page after they login, which is a nicer user experience
		// than dropping them off on the home page.
		return <Navigate to="/" state={{ from: location }} replace />;
	}

	return children;
}

function App() {
	return (
		<>
			<BrowserRouter>
				<Routes>
					<Route path='/' element={<LoginPage />} />
					<Route path='products' element={<RequireAuth><ProductsPage /></RequireAuth>} />
					<Route path='phases' element={<RequireAuth><PhasesPage /></RequireAuth>} />
					<Route path='compounds' element={<RequireAuth><CompoundsPage /></RequireAuth>} />
					<Route path='comments' element={<RequireAuth><CommentsPage /></RequireAuth>} />
					<Route path='filters' element={<RequireAuth><GreenFiltersPage /></RequireAuth>} />
				</Routes>
			</BrowserRouter>
			<ToastContainer
				position="bottom-right"
				autoClose={5000}
				hideProgressBar={false}
				newestOnTop
				closeOnClick
				rtl={false}
				pauseOnFocusLoss
				draggable
				pauseOnHover
				theme="dark"
			/>
		</>
	);
}

export default App;
