import { useSelector } from "react-redux";
import { selectUser } from "../../../../app/slices/userSlice";
import "./Metric.css";


import { confirmDialog } from 'primereact/confirmdialog'; // To use confirmDialog method
import { useMutation, useQueryClient } from "react-query";

import { toast } from "react-toastify";
import api from "../../../../services/api";
import { EditMetric } from "./EditMetric/EditMetric";

export const Metric = (props) => {
    const metric = props.data
    const user = useSelector(selectUser)
    const client = useQueryClient()
    const restraintValues = [metric.min_value, metric.value, metric.max_value].sort((a, b) => a - b)

    const deleteMetric = useMutation(() => api.delete("filters/parametrics/" + metric.id).then(() => {
        client.invalidateQueries("params")
        toast.success("Métrica deletada com sucesso!")
    }).catch(() => toast.error("Ocorreu um erro ao deletar a métrica"))
    )

    const confirm = () => {
		confirmDialog({
			message: `Tem certeza que deseja deletar essa métrica?`,
			header: 'Deletar métrica',
			icon: 'pi pi-exclamation-triangle',
			accept: () => deleteMetric.mutate(),
			reject: () => null
		});
	}

    return (
        <div className="metric">
            <div style={{display: "flex", gap: "0.65rem", alignItems: "center"}}>
                <p>{metric.metric}</p>
                {user.greenfilters_access === "FULL" || user.greenfilters_access === "PARAMS" ?
                    <>
                        <EditMetric data={metric}/>
                        <i className="pi pi-trash" onClick={confirm}/>
                    </> : null
                }
            </div>
            <div className="restraints">
                {restraintValues.map((value, index) => {
                    if (!value) return null
                    let type = "";

                    if (value === metric.min_value) type = "min";
                    if (value === metric.value) type = "atual";
                    if (value === metric.max_value) type = "max";

                    return (
                        <div className={`restr ${type}`} key={index}>
                            <p>{type.charAt(0).toUpperCase() + type.slice(1)}</p>
                            <p>{value}</p>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}