import React, { useEffect, useState } from 'react';

import { ButtonBase } from '@mui/material';
import { ProductCard } from '../../../ProductCard/ProductCard';
import ModalBase from '../../../shared/ModalBase/ModalBase';
import AddProduct from '../../Products/AddProduct/AddProduct';
import ViewProduct from '../../Products/ViewProduct/ViewProduct';

import { Dropdown } from 'primereact/dropdown';

import { useQueryClient } from 'react-query';
import AsyncSelect from 'react-select/async';
import usePhases from '../../../../hooks/PhaseHandlers';
import api from '../../../../services/api';
import './AddProductPhase.css';

export default function AddProductPhase(props) {
    const { phases } = usePhases()
    const qClient = useQueryClient()
    
    const [details, setDetails] = useState({
        product: "",
        phase: props.phase ? props.phase : ""
    })

    useEffect(() => {
        props.backdropAutoClose(false)
    }, [])

    const setPhaseProduct = (value) => {
        api.get(`products/${value.id}/`).then(res => {
            if (res.status === 200) {
                setDetails({...details, product: res.data})
            }
        }).catch(() => {
            alert("Ocorreu um problema ao retirar o novo produto, por favor selecione-o manualmente.")
        })
    }

    const handleSave = () => {
        if (details.product && details.phase) {
            props.addProduct(details.product, details.phase)
            qClient.invalidateQueries(["phase"])
            props.closeModal()
        } else {
            alert("Por favor verifique os dados inseridos")
        }
    }

    const loadOptions = (inputValue, callback) => {
        api.get(
            `products/dropdown?limit=15&search=${inputValue}`
        ).then(res => callback(res.data.results))
    };

    return (
        <div className='viewProductPhase'>
            <div className="p-grid">
                <div className="p-col">
                    <div className='productContainer'>
                        {details.product ? (
                            <ModalBase key={details.product.id}
                                caller={<ProductCard prodId={details.product.id} />}
                                content={<ViewProduct productId={details.product.id} />}
                                title={details.product.reference}
                            />
                        ) : (
                            <h3>Selecione um PRODUTO para exibir...</h3>
                        )}
                    </div>
                </div>
                <div className="p-col">
                    <div className='detailsContainer p-grid p-dir-col'>
                        <div className="pcol">
                            <div className="p-grid p-dir-col">
                                <div className="p-col details--product">
                                    <h4>Produto</h4>
                                    <AsyncSelect onChange={(selected) => setDetails({ ...details, product: selected })}
                                        styles={{
                                            container: (baseStyles, state) => ({
                                              ...baseStyles,
                                              width: "100%",
                                              height: "2.5rem"
                                            }),
                                            option: (baseStyles, state) => ({
                                                ...baseStyles,
                                                color: "black",
                                              }),
                                          }}
                                        cacheOptions placeholder="Selecionar produto"
                                        loadOptions={loadOptions}
                                        defaultOptions
                                        getOptionLabel={(obj) => obj.reference}
                                        getOptionValue={(obj) => obj.id}
                                    />
                                    <p>ou</p>
                                    <ModalBase
                                        caller={<div className='details--createProduct'>Criar novo produto</div>}
                                        content={<AddProduct setNewProduct={setPhaseProduct}/>}
                                        title="Adicionar PRODUTO ao PROCESSO"
                                    />
                                    
                                </div>
                                <div className="p-col">
                                    <h4>Fase</h4>
                                    <Dropdown value={details.phase} 
                                        onChange={(e) => setDetails({...details, phase: e.target.value})}
                                        options={phases}
                                        optionLabel='phase'
                                        placeholder='Selecione uma fase'
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="pcol buttons">
                            <ButtonBase className='addButton' onClick={handleSave}>Adicionar</ButtonBase>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}