import { useCallback, useContext, useRef } from "react"
import { FilteredComments, useFilteredComments } from "../.."
import { InlineComment } from "../../InlineComment/InlineComment"

import './ItemComments.css'

export const ItemComments = () => {
    const filters = useContext(FilteredComments)
    const { 
        data,
        isFetchingNextPage,
        hasNextPage,
        fetchNextPage,
        status
    } = useFilteredComments("comments?type=item", filters)
    
    const intObserver = useRef()

    const lastPostRef = useCallback((post) => {
        if (isFetchingNextPage) return

        if (intObserver.current) intObserver.current.disconnect()

        intObserver.current = new IntersectionObserver(posts => {
            if (posts[0].isIntersecting && hasNextPage) {
                fetchNextPage()
            }
        })

        if (post) intObserver.current.observe(post)
    }, [isFetchingNextPage, fetchNextPage, hasNextPage])

    if (status === "loading") return <>Carregando...</>
    if (status === "error") return <>Ocorreu um erro tentando retirar os comentários, por favor tente novamente.</>

    return (
        <div className="com-container">
            {
                data?.pages.map((page, index) => <div key={index}>{
                    page.results.map((comment, index) => {
                        if (page.results.length === index + 1) {
                            return <InlineComment comment={comment} key={comment.id} ref={lastPostRef}/>
                        }

                        return <InlineComment comment={comment} key={comment.id}/>
                    })
                }</div>)
            }
            {isFetchingNextPage && <p>Carregando mais páginas...</p>}
        </div>
    )
}