import { confirmDialog } from 'primereact/confirmdialog'; // To use confirmDialog method
import { useMutation, useQueryClient } from "react-query"
import { useSelector } from "react-redux"
import { toast } from "react-toastify"
import { selectUser } from "../../../../app/slices/userSlice"
import api from "../../../../services/api"
import { Metric } from "../Metric/Metric"
import { AddMetricModal } from "./AddMetricModal/AddMetricModal"
import "./ParamContainer.css"

export const ParamContainer = (props) => {
    const param = props.data
    const user = useSelector(selectUser)
    const client = useQueryClient()

    const deleteParam = useMutation(() => api.delete("filters/params/" + param.id).then(() => {
        client.invalidateQueries("params")
        client.invalidateQueries(["filters", "available"])
        toast.success("Parâmetro deletado com sucesso!")
    }).catch(() => toast.error("Ocorreu um erro ao deletar o parâmetro"))
    )
    
    const confirm = () => {
		confirmDialog({
			message: `Tem certeza que deseja deletar esse parâmetro?`,
			header: 'Deletar parâmetro',
			icon: 'pi pi-exclamation-triangle',
			accept: () => deleteParam.mutate(),
			reject: () => null
		});
	}

    return (
        <div className="p-ct">
            <div className="heading">
                <h3>Parâmetro {param.filter}</h3>
                {user.greenfilters_access === "FULL" || user.greenfilters_access === "PARAMS" ?
                    <AddMetricModal param={param.id} /> : null
                }
                {(user.greenfilters_access === "FULL" || user.greenfilters_access === "PARAMS") && param.parametrics.length === 0 ?
                    <i className="pi pi-trash" onClick={confirm}/> : null
                }
            </div>
            <div className="metrics">
                {
                    param.parametrics.map(metric => <Metric key={metric.id} data={metric} />)
                }
            </div>
        </div>
    )
}