import React, { useEffect, useState } from 'react';

import { InputNumber } from "primereact/inputnumber";

import { useEstimative } from '../../../../hooks/EstimativeHandlers';
import './Estimative.css';

export default function Estimative(props) {
    const data = props.data
    const { 
        isLoading, isError, estimative,
        patchEstimative, deleteEstimative
    } = useEstimative(props.data.estimative)

    const [details, setDetails] = useState({
        assortment: data.assortment,
        consumption: data.consumption
    })

    const [isDelete, setDelete] = useState(false)
    const [changed, setChanged] = useState(false)

    useEffect(() => {
        if (!isLoading && !isError) {
            setDetails({
                assortment: estimative.assortment,
                consumption: estimative.consumption
            })
        }
    }, [estimative])

    useEffect(() => {
        if (details && changed)
            !isDelete && props.changeEstimativeData(props.data.estimative, details);
    }, [details])

    useEffect(() => {
        let signal = props.materialSignal
        let handleSave = async () => {
            let res = await patchEstimative(details)
            if (res.status === 403) {
                setDetails({
                    assortment: data.assortment,
                    consumption: data.consumption
                })
            }
        }
        
        if (signal === "cancel") {
            setDetails({
                assortment: data.assortment,
                consumption: data.consumption
            })
        } else if (signal === "save") {
            if (isDelete) deleteEstimative(props.prodId);
            else handleSave()
        }
    }, [props.materialSignal])

    return (
        <tr className={`mat-estimative ${isDelete && "deleted"}`}>
            <td className='mat-color'>
                {data.code} / {data.description} 
            </td>
            <td>
                <InputNumber className='mat-consumption'
                    value={details.consumption}
                    locale="pt-BR"
                    mode="decimal"
                    showButtons
                    min={0}
                    minFractionDigits={2}
                    maxFracionDigits={2}
                    onValueChange={(e) => {
                        setChanged(true)
                        setDetails((d) => ({ ...d, consumption: e.value }));
                    }}
                    disabled={!props.editing || isDelete}
                />
            </td>
            <td>
                <InputNumber className='mat-assortment'
                    value={details.assortment}
                    locale="pt-BR"
                    suffix='%'
                    showButtons
                    min={0}
                    max={100}
                    onValueChange={(e) => {
                        setChanged(true)
                        setDetails((d) => ({ ...d, assortment: e.value }));
                    }}
                    disabled={!props.editing || isDelete}
                />
            </td>
            {props.editing &&
                <td id="delete-est" onClick={() => {
                    props.changeEstimativeData(props.data.estimative, {assortment: 0, consumption: 0})
                    setDelete(true)
                }}>
                    <i className='pi pi-times'/>
                </td>
            }
        </tr>
    )
}