import { ButtonBase } from "@mui/material";
import { Calendar } from "primereact/calendar";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";
import React, { useEffect, useState } from "react";
import { useQuery } from "react-query";
import { useDemands } from "../../../../hooks/DemandsHandlers";
import api from "../../../../services/api";
import "./AddDemand.css";

export default function AddDemand(props) {
	const { data: storesWithDemands } = useQuery(
		["storesWithDemands", props.item.id],
		() => api.get("compounds/items/" + props.item.id + "/open_stores").then(res => res.data)
	)

	const [details, setDetails] = useState({
		store: "",
		quantity: "",
		date: "",
		production_date: "",
	});
	const [dateView, setDateView] = useState();
	const [prodDateView, setProdDateView] = useState();
	const [dateRestriction, setDateRestriction] = useState({
		start: null,
		end: null,
	});

	useEffect(() => {
		let deltaStart = props.period.start_date.split("-");
		let dateStart = new Date();
		dateStart.setDate(deltaStart[0]);
		dateStart.setMonth(deltaStart[1] - 1);
		dateStart.setFullYear(deltaStart[2]);

		setDateRestriction((d) => ({ ...d, start: dateStart }));

		let deltaEnd = props.period.end_date.split("-");
		let dateEnd = new Date();
		dateEnd.setDate(deltaEnd[0]);
		dateEnd.setMonth(deltaEnd[1] - 1);
		dateEnd.setFullYear(deltaEnd[2]);

		setDateRestriction((d) => ({ ...d, end: dateEnd }));
	}, []);

	useEffect(() => {
		let delta = details.date.split("-");
		let date = new Date();
		date.setDate(delta[0]);
		date.setMonth(delta[1] - 1);
		date.setFullYear(delta[2]);

		setDateView(date);
	}, [details.date]);

	useEffect(() => {
		let delta = details.date.split("-");
		let date = new Date();
		date.setDate(delta[0]);
		date.setMonth(delta[1] - 1);
		date.setFullYear(delta[2]);

		setProdDateView(date);
	}, [details.production_date]);

	const { addDemand } = useDemands(props.item.id);

	const handleSaving = () => {
		addDemand(props.item.id, details.store, details.quantity, details.date, details.production_date);
		props.close();
	};

	return (
		<div className="grid">
			<div className="addDemand">
				<div className="p-col">
					<Dropdown
						value={details.store}
						onChange={(e) => {
							setDetails({ ...details, store: e.value });
						}}
						options={storesWithDemands}
						optionLabel="name"
						optionValue="id"
						placeholder="Selecione uma loja"
					/>
				</div>
				<div className="p-col">
					<Calendar
						value={dateView}
						dateFormat="dd/mm/yy"
						placeholder="Data"
						minDate={dateRestriction.start}
						maxDate={dateRestriction.end}
						onChange={(e) => {
							setDetails((d) => ({
								...d,
								date: `${e.value.getDate()}-${e.value.getMonth() + 1
									}-${e.value.getFullYear()}`,
							}));
						}}
					/>
				</div>
				<div className="p-col">
					<Calendar
						value={prodDateView}
						dateFormat="dd/mm/yy"
						placeholder="Data de produção"
						onChange={(e) => {
							setDetails((d) => ({
								...d,
								production_date: `${e.value.getDate()}-${e.value.getMonth() + 1
									}-${e.value.getFullYear()}`,
							}));
						}}
					/>
				</div>
				<div className="p-col">
					<InputNumber
						value={details.quantity}
						locale="pt-BR"
						showButtons
						placeholder="Quantidade"
						min={1}
						minFractionDigits={0}
						maxFracionDigits={0}
						onValueChange={(e) => {
							setDetails({
								...details,
								quantity: e.value,
							});
						}}
					/>
				</div>
			</div>
			<ButtonBase onClick={(e) => {
				handleSaving()
			}} className="greenButton">
				Salvar
			</ButtonBase>
		</div>
	);
}
