import React, { useState } from 'react';

import { ButtonBase } from '@mui/material';

import './EditSaveButton.css';

export default function EditSaveButton(props) {
    const [editing, setEditing] = useState(false)

    return (
        <div className='editSaveButtons'>
            {editing ? (
                <div className="p-grid p-dir-col">
                    <div className="p-col">
                        <ButtonBase className='button cancel' 
                        onClick={() => {
                            props.onCancel()
                            setEditing(false)
                        }}
                        >Cancelar</ButtonBase>
                    </div>
                    <div className="p-col">
                        <ButtonBase className='button save' 
                        onClick={() => {
                            props.onSave()
                            setEditing(false)
                        }}>Salvar</ButtonBase>
                    </div>
                </div>
            ) : (
                <div className="p-grid p-dir-col">
                    <div className="p-col">
                        <ButtonBase className='button edit' 
                        onClick={() => {
                            props.onEdit()
                            setEditing(true)
                        }}>Editar</ButtonBase>
                    </div>
                </div>
            ) }
        </div>
    )
}