import { Modal } from "@mui/material"
import { useState } from "react"

import api from "../../../../services/api"
import "./ImportProductModal.css"


export const ImportProductModal = () => {
    const [open, setOpen] = useState(false)
    const [file, setFile] = useState(undefined)
    const [feedback, setFeedback] = useState(null)

    const handleSaving = () => {
        const postData = new FormData()
        postData.append("file", file)

        api.post('products/import', postData, {timeout: 8 * 60 * 1000}).then(res => {
            setFeedback(res.data)
        }).catch(err => {
            if (err.code == "ECONNABORTED") {
                alert("Tempo máximo de requisição atingido!")
            } else {
                alert("Ocorreu um erro ao importar a planilha")
            }
        })
    }

    const handleClose = () => {
        setOpen(false)
        setFile(undefined)
        setFeedback(null)
    }

    return (
        <>
            <div className='FAB footerButton' onClick={() => setOpen(true)}>
                <i className='pi pi-cloud-upload' style={{fontSize: "1.25rem"}}/>
            </div>
            <Modal open={open} onClose={handleClose}>
                <div id="impp-md-ct">
                    <div id="impp-md">
                        <div className="header">
                            <h3>Importar produtos</h3>
                            <i className="pi pi-times" onClick={handleClose}/>
                        </div>
                        <div className="feedback">
                        {file ? 
                            <h4>
                                Arquivo selecionado: {file.name}
                            </h4> 
                            : 
                            <h4>
                                Nenhum arquivo selecionado!
                            </h4>
                        }
                        {feedback &&
                            Object.keys(feedback).map(fd => {
                                let fdb = feedback[fd]

                                if (fdb.status === "success") return (
                                    <p style={{display: "flex", gap: "0.25rem"}} key={fd}>
                                        {fd}: <b style={{color: "var(--app-green)"}}>{fdb.response}</b>
                                    </p>
                                )
                                else return (
                                    <div className="fb-errors" key={fd}>
                                        <p>{fd}:</p>
                                        {fdb.response.process_errors.map((error, index) => 
                                            <p style={{color: "var(--app-salmon)", marginLeft: "0.85rem"}} key={index}>
                                                {error}
                                            </p>
                                        )}
                                    </div>
                                )
                            })
                        }
                        </div>
                        <div className="footer">
                            <label className="selector" htmlFor='selectButton'>Selecionar um arquivo</label>
                            <input id='selectButton'
                                type="file" 
                                accept='.csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel'
                                onChange={(e) => {
                                    setFile(e.target.files[0])
                                }}
                            />
                            {file && !feedback ? 
                                <button className='uploadButton' onClick={handleSaving}>
                                    Processar
                                </button> 
                                : 
                                <button className='uploadButton' onClick={handleClose}>
                                    Concluir
                                </button>
                            }
                        </div>
                    </div>
                </div>
            </Modal>
        </>
    )
}