import React, { Fragment, useCallback, useEffect, useRef, useState } from 'react';

import AddProductPhase from '../Modals/Phases/AddProductPhase/AddProductPhase';
import ViewProductPhase from '../Modals/Phases/ViewProductPhase/ViewProductPhase';
import { ProductCard } from '../ProductCard/ProductCard';
import ModalBase from '../shared/ModalBase/ModalBase';

import { usePhaseProducts } from '../../hooks/PhaseHandlers';
import './PhaseCard.css';

export default function PhaseCard(props) {
    const { 
        products, 
        isLoading, 
        updatePhase,
        setFilters,
        phaseOperations,
        isFetching,
        isFetchingNextPage,
        fetchNextPage,
        hasNextPage
    } = usePhaseProducts(props.phase.id)

    useEffect(() => {
        let filters = {...props.filters}
        setFilters(filters)   
    }, [props.filters])

    const intObserver = useRef()
    const lastItemRef = useCallback((item) => {
        if (isFetchingNextPage) return

        if (intObserver.current) intObserver.current.disconnect()

        intObserver.current = new IntersectionObserver(items => {
            if (items[0].isIntersecting && hasNextPage) {
                fetchNextPage()
            }
        })

        if (item) intObserver.current.observe(item)
    }, [isFetchingNextPage, fetchNextPage, hasNextPage])

    return (
        <div className="phaseCard">
            <div className="phaseCard--title">
                <h3>{props.title.toUpperCase()}</h3>
                <p>|</p>
                <h3>{products ? products.pages[0].count : 0}</h3>
            </div>
            <div className="phaseCard--body">
                {!isLoading &&
                    <ModalBase
                        caller={
                            <div className='FAB'>
                                <h1>+</h1>
                            </div>
                        }
                        content={
                            <AddProductPhase 
                                phase={props.phase} 
                                onClose={() => updatePhase()}
                                addProduct={phaseOperations.add}
                            />
                        }
                        title="Adicionar PRODUTO ao PROCESSO"
                    />
                }
                {isFetching && !products &&
                    <i className="pi pi-spin pi-spinner m-auto" style={{color: "#A3AE9E", fontSize: "2rem"}} />
                }
                {products?.pages[0].count === 0 &&
                    <p className="m-auto" style={{color: "#A3AE9E", fontWeight: 700, fontSize: "1.125rem"}}>Nenhum produto encontrado :(</p>
                }
                {products && products?.pages[0].count != 0 &&
                    <div className="flex flex-wrap p-4">
                        {
                            products?.pages.map((page, index) => <Fragment key={index}>{
                                page.results.map((product, index) => {
                                    if (page.results.length === index + 1) {
                                        return (
                                            <ModalBase  key={product.id}
                                                caller={
                                                    <ProductCard 
                                                        key={product.images[0]?.id} 
                                                        data={product} compact hidePhase 
                                                        showDaysOnPhase showIsShowcase
                                                        ref={lastItemRef}
                                                    />
                                                }
                                                content={<ViewProductPhase 
                                                    data={product} 
                                                    phase={props.phase}
                                                    allPhases={props.allPhases}
                                                    phaseOperations={phaseOperations}
                                                />}
                                                title="Visualizar PRODUTO em PROCESSO"
                                                onClose={() => {
                                                    updatePhase()
                                                }}
                                            />
                                        )
                                    }

                                    return (
                                        <ModalBase  key={product.id}
                                            caller={
                                                <ProductCard 
                                                    key={product.images[0]?.id} 
                                                    data={product} compact hidePhase 
                                                    showDaysOnPhase showIsShowcase
                                                />
                                            }
                                            content={<ViewProductPhase 
                                                data={product} 
                                                phase={props.phase}
                                                allPhases={props.allPhases}
                                                phaseOperations={phaseOperations}
                                            />}
                                            title="Visualizar PRODUTO em PROCESSO"
                                            onClose={() => {
                                                updatePhase()
                                            }}
                                        />
                                    )
                                })
                            }</Fragment>)
                        }
                    </div>
                }
                {isFetchingNextPage && <p>Carregando mais produtos...</p>}
            </div>
        </div>
    )
}