import { forwardRef } from "react";

import CompoundPopover from '../CompoundPopover/CompoundPopover';
import DuplicateItem from '../DuplicateItem/DuplicateItem';
import ViewProductCompound from '../Modals/Compounds/ViewProductCompound/ViewProductCompound';
import { ProductCard } from '../ProductCard/ProductCard';
import ModalBase from '../shared/ModalBase/ModalBase';


import "./ItemCard.css";

export const ItemCard = forwardRef((props, ref) => {
    const item = props.item;

    return (
        <div className="itemCard" ref={ref}>
            <CompoundPopover item={item} />
            <DuplicateItem item={item} copyItem={props.copyItem} />
            <ModalBase
                buttonId={"itemCard--bt"}
                caller={
                    <ProductCard
                        data={item.product} typed={item.typed}
                        genericImg={
                            item.product.generic ?
                                item.image : undefined
                        }
                        key={
                            item.product.generic && item.image
                                ? item.image : item.reference
                        }
                        adaptable
                    />
                }
                content={<ViewProductCompound
                    data={item} period={props.period}
                    genericImg={item.image}
                    updateItems={props.updateItems}
                />}
                onClose={props.updateItems}
                title="Visualizar PRODUTO em COMPOSTO"
            />
        </div>
    );
});
