import React from 'react';

import ModalBase from '../../../shared/ModalBase/ModalBase';

import { useProductEstimatives } from '../../../../hooks/EstimativeHandlers';
import MaterialContainer from '../MaterialContainer/MaterialContainer';

import AddMaterialEstimative from '../AddMaterialEstimative/AddMaterialEstimative';
import './EstimativesByMaterial.css';

export default function EstimativesByMaterial(props) {
    const { estimatives, addProductEstimative } = useProductEstimatives(props.product.id)
    props.backdropAutoClose(false);

    return (
        <div className='estimatives-by-material'>
            <div className='ebm'>
                {estimatives &&
                    estimatives.map((estimative) => (
                        <MaterialContainer 
                            data={estimative}
                            prodId={props.product.id} 
                            key={estimative.material.id}
                            addEstimative={addProductEstimative}
                        />
                    ))
                }
            </div>
            <ModalBase 
                caller={<div className='add-material-button'>Adicionar novo material</div>} 
                content={<AddMaterialEstimative 
                    prodId={props.product.id} 
                    addEstimative={addProductEstimative}
                    currentEstimatives={estimatives}
                />} 
                title="Adicionar estimativa de material"
            />
        </div>
    )
}