import { ButtonBase } from "@mui/material";
import { confirmDialog } from "primereact/confirmdialog";
import { Divider } from "primereact/divider";
import { Dropdown } from "primereact/dropdown";
import { Galleria } from "primereact/galleria";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from 'primereact/inputtextarea';
import { MultiSelect } from "primereact/multiselect";
import React, { useEffect, useState } from "react";

import ModalBase from "../../../shared/ModalBase/ModalBase";

import { Checkbox } from 'primereact/checkbox';
import { useQuery } from "react-query";
import usePhases from "../../../../hooks/PhaseHandlers";
import { useProduct } from "../../../../hooks/ProductHandlers";
import useProps from "../../../../hooks/PropsHandlers";
import api from "../../../../services/api";
import { juntarArrays } from "../../../../utils/arrays";
import EstimativesByMaterial from "../../Estimatives/EstimativesByMaterial/EstimativesByMaterial";
import ManageImages from "../ManageImages/ManageImages";
import ViewLogProduct from "../ViewLogProduct/ViewLogProduct";
import "./ViewProduct.css";

export default function ViewProduct(props) {
	const { product, deleteProduct, patchProduct, updateProduct } = useProduct(props.productId);
	const { updatePhases } = usePhases()
	const { data: families } = useQuery(
		"sewing_families", 
		() => api.get("products/families").then(res => res.data), 
		{
			refetchOnWindowFocus: false,
			refetchOnMount: false
		}
	)
	const { data: cut_families } = useQuery(
		"cut_families", 
		() => api.get("products/cut_families").then(res => res.data), 
		{
			refetchOnWindowFocus: false,
			refetchOnMount: false
		}
	)

	const [details, setDetails] = useState(product);

	useEffect(() => {
		setDetails(product);
	}, [product]);

	const [edited, setEdited] = useState({});

	const [editing, setEditing] = useState(false);

	useEffect(() => {
		props.backdropAutoClose(!editing);
	}, [editing]);

	const {
		lines,
		groups,
		collections,
		stylists,
		designers,
		productsBase,
	} = useProps();

	const { data: productTags } = useQuery(
        "productTags", 
        () => api.get("tags/types?type=1").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )
    const { data: processTags } = useQuery(
        "processTags", 
        () => api.get("tags/types?type=2").then(res => res.data),
        {
            refetchOnMount: false,
            refetchOnWindowFocus: false
        }
    )

	const itemTemplate = (item) => {
		if (editing) {
			return (
				<ModalBase
					caller={
						<div className="editCaller">
							<span>+</span>
							<img
								src={item?.image}
								alt={`imagem de ${details?.reference}`}
								className="headImage"
							/>
						</div>
					}
					content={<ManageImages product={product} />}
					title="Adicionar imagem"
				/>
			);
		} else {
			return (
				<ModalBase
					caller={
						<img
							src={item?.image}
							alt={`imagem de ${details?.reference}`}
							className="headImage"
						/>
					}
					content={
						<img src={item?.image} alt={`imagem de ${details?.reference}`} />
					}
					title="Visualizar imagem"
				/>
			);
		}
	};

	const thumbnailTemplate = (item) => {
		return <img src={item.image} alt={item.alt} className="gallery_thumb" />;
	};

	const handleEdit = async () => {
		setEditing(!editing);

		let patchData = edited;
		Object.keys(patchData).forEach((key) => {
			if (patchData[key] === undefined) {
				patchData[key] = null
			} else if (patchData[key]?.id !== undefined) {
				patchData[key] = patchData[key].id
			}
		});

		if (patchData.product_tags && patchData.process_tags) {
			let tags = juntarArrays(patchData.process_tags, patchData.product_tags)
			patchData = { ...patchData, tags: tags };
		} 
		else if (patchData.product_tags && !patchData.process_tags) {
			let tags = juntarArrays(details.process_tags, patchData.product_tags)
			patchData = { ...patchData, tags: tags };
		} 
		else if (!patchData.product_tags && patchData.process_tags) {
			let tags = juntarArrays(patchData.process_tags, details.product_tags)
			patchData = { ...patchData, tags: tags };
		}

		patchProduct(patchData);
		updatePhases()
	};

	const handleDelete = () => {
		if (!product.registered_ERP) {
			deleteProduct();
			props.closeModal();
		} else {
			alert(
				"Produto cadastrado pelo ERP, por favor delete-o pela interface de administrador!"
			);
		}
	};

	const confirmDeletion = () => {
		confirmDialog({
			message: "Tem certeza que deseja deletar esse PRODUTO?",
			header: "Deletar produto",
			icon: "pi pi-exclamation-triangle",
			acceptLabel: "Sim",
			rejectLabel: "Não",
			accept: handleDelete,
			reject: () => { },
		});
	};

	return (
		<div className="viewProduct">
			<div className="p-grid p-justify-start">
				<div className="p-col imageContainer">
					{details?.images.length === 0 ? (
						<ModalBase
							caller={
								<img
									src="/assets/images/ProdutoSemMidia.svg"
									alt="produto sem mídia"
									className="headImage"
								/>
							}
							content={<ManageImages product={product} />}
							title="Adicionar imagem"
							onClose={() => updatePhases()}
						/>
					) : (
						<Galleria
							value={details?.images}
							item={itemTemplate}
							thumbnail={thumbnailTemplate}
							numVisible={3}
							circular
						/>
					)}
				</div>
				<div className="p-col">
					<div className="p-grid p-dir-col productDetails">
						<div className="p-col">
							<h4>Tags de produto</h4>
							<MultiSelect
								optionLabel="name"
								optionValue="id"
								value={details?.product_tags}
								options={productTags}
								display="chip"
								filter
								optionGroupLabel="name"
								optionGroupChildren="tags"
								onChange={(e) => {
									setDetails({ ...details, product_tags: e.target.value });
									setEdited({ ...edited, product_tags: e.target.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Tags de processo</h4>
							<MultiSelect
								optionLabel="name"
								optionValue="id"
								value={details?.process_tags}
								options={processTags}
								display="chip"
								filter
								optionGroupLabel="name"
								optionGroupChildren="tags"
								onChange={(e) => {
									setDetails({ ...details, process_tags: e.target.value });
									setEdited({ ...edited, process_tags: e.target.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Descrição</h4>
							<InputText
								value={details?.description ? details.description : ""}
								onChange={(e) => {
									setDetails({ ...details, description: e.target.value });
									setEdited({ ...edited, description: e.target.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Linha</h4>
							<Dropdown
								value={details?.line}
								onChange={(e) => {
									if (!details?.registered_ERP) {
										setDetails({ ...details, line: e.value });
										setEdited({ ...edited, line: e.value });
									}
								}}
								disabled={!editing || details?.registered_ERP}
								options={lines}
								optionLabel="name"
								placeholder="Selecione uma linha"
							/>
						</div>
						<div className="p-col">
							<h4>Grupo</h4>
							<Dropdown
								value={details?.group}
								onChange={(e) => {
									if (!details?.registered_ERP) {
										setDetails({ ...details, group: e.value });
										setEdited({ ...edited, group: e.value });
									}
								}}
								disabled={!editing || details?.registered_ERP}
								options={groups}
								optionLabel="name"
								placeholder="Selecione um grupo"
							/>
						</div>
						<div className="p-col">
							<h4>Subgrupo</h4>
							<Dropdown
								value={details?.subgroup}
								onChange={(e) => {
									if (!details?.registered_ERP) {
										setDetails({ ...details, subgroup: e.value });
										setEdited({ ...edited, subgroup: e.value });
									}
								}}
								disabled={!editing || details?.registered_ERP}
								options={
									edited.group
										? edited.group.subgroups
										: details?.group.subgroups
								}
								optionLabel="name"
								placeholder="Selecione um subgrupo"
							/>
						</div>
						<div className="p-col">
							<h4>Coleção</h4>
							<Dropdown
								value={details?.collection}
								onChange={(e) => {
									setDetails({ ...details, collection: e.value });
									setEdited({ ...edited, collection: e.value });
								}}
								disabled={!editing || details?.registered_ERP}
								options={collections}
								optionLabel="name"
								placeholder="Selecione uma coleção"
								showClear
							/>
						</div>
						<div className="p-col">
							<h4>Estilista</h4>
							<Dropdown
								value={details?.stylist}
								onChange={(e) => {
									setDetails({ ...details, stylist: e.value });
									setEdited({ ...edited, stylist: e.value });
								}}
								disabled={!editing}
								options={stylists}
								optionLabel="name"
								placeholder="Selecione um estilista"
								showClear
							/>
						</div>
						<div className="p-col">
							<h4>Designer</h4>
							<Dropdown
								value={details?.designer}
								onChange={(e) => {
									setDetails({ ...details, designer: e.value });
									setEdited({ ...edited, designer: e.value });
								}}
								disabled={!editing}
								options={designers}
								optionLabel="name"
								placeholder="Selecione um designer"
								showClear
							/>
						</div>
						<div className="p-col">
							<h4>Descrição técnica</h4>
							<InputTextarea autoResize 
								value={details?.comments ? details.comments : ""}
								onChange={(e) => {
									setDetails({ ...details, comments: e.target.value });
									setEdited({ ...edited, comments: e.target.value });
								}}
								maxLength={500}
								disabled={!editing}
							/>
						</div>
						<div className="p-col" style={{display: "flex", alignItems: "center", gap: "0.5rem"}}>
							<Checkbox inputId="cb2" 
								onChange={e => {
									setDetails({ ...details, released: e.checked });
									setEdited({ ...edited, released: e.checked });
								}} 
								checked={details?.released}
								disabled={!editing} 
							/>
							<label htmlFor="cb2" className="p-checkbox-label">Liberado</label>
						</div>

						<Divider />
						<div className="p-col">
							<h3>Pré-cadastro</h3>
						</div>

						<div className="p-col">
							<h4>Família de costura</h4>
							<Dropdown
								value={details?.sewing_family}
								onChange={(e) => {
									setDetails({ ...details, sewing_family: e.value });
									setEdited({ ...edited, sewing_family: e.value });
								}}
								disabled={!editing}
								options={families}
								optionLabel="name"
								optionValue="id"
								placeholder="Selecione uma família de costura"
								showClear
							/>
						</div>

						<div className="p-col">
							<h4>Família de corte</h4>
							<Dropdown
								value={details?.cut_family}
								onChange={(e) => {
									setDetails({ ...details, cut_family: e.value });
									setEdited({ ...edited, cut_family: e.value });
								}}
								disabled={!editing}
								options={cut_families}
								optionLabel="name"
								optionValue="id"
								placeholder="Selecione uma família de corte"
								showClear
							/>
						</div>

						<div className="p-col">
							<h4>Meta custo</h4>
							<InputNumber
								value={details?.goal_cost}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								min={0}
								minFractionDigits={2}
								maxFracionDigits={2}
								onValueChange={(e) => {
									setDetails({ ...details, goal_cost: e.value });
									setEdited({ ...edited, goal_cost: e.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Preço alvo</h4>
							<InputNumber
								value={details?.target_price}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								min={0}
								minFractionDigits={2}
								maxFracionDigits={2}
								onValueChange={(e) => {
									setDetails({ ...details, target_price: e.value });
									setEdited({ ...edited, target_price: e.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Custo de customização</h4>
							<InputNumber
								value={details?.customization_cost}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								min={0}
								minFractionDigits={2}
								maxFracionDigits={2}
								onValueChange={(e) => {
									setDetails({ ...details, customization_cost: e.value });
									setEdited({ ...edited, customization_cost: e.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Custo de ajustes</h4>
							<InputNumber
								value={details?.adjustments_cost}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								minFractionDigits={2}
								maxFracionDigits={2}
								onValueChange={(e) => {
									setDetails({ ...details, adjustments_cost: e.value });
									setEdited({ ...edited, adjustments_cost: e.value });
								}}
								disabled={!editing}
							/>
						</div>
						<div className="p-col">
							<h4>Pré custo</h4>
							<InputNumber
								value={details?.pre_cost}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								min={0}
								minFractionDigits={2}
								maxFracionDigits={2}
								disabled={true}
							/>
						</div>
						<div className="p-col">
							<h4>Base Produto</h4>
							<Dropdown
								value={details?.product_base}
								onChange={(e) => {
									setDetails({ ...details, product_base: e.value });
									setEdited({ ...edited, product_base: e.value });
								}}
								disabled={!editing}
								options={productsBase}
								optionLabel="reference"
								placeholder="Selecione uma base produto"
								showClear
							/>
						</div>
						<div className="p-col materialestimativeCaller">
							<ModalBase
								title="Estimativas de material"
								caller={
									<div>
										<p>Visualizar estimativas de material</p>
									</div>
								}
								content={<EstimativesByMaterial product={product} />}
								onClose={updateProduct}
							/>
						</div>
						<div className="p-col materialestimativeCaller log">
							<ModalBase
								title="Histórico do Produto"
								caller={
									<div>
										<p>Histórico do produto</p>
									</div>
								}
								content={<ViewLogProduct productId={product?.id} />}
							/>
						</div>
						<div className="p-col">
							<div className="p-grid">
								<div className="p-col">
									{!editing ? (
										<ButtonBase
											onClick={() => setEditing(!editing)}
											className="greenButton"
										>
											Editar
										</ButtonBase>
									) : (
										<ButtonBase onClick={confirmDeletion} className="redButton">
											Deletar
										</ButtonBase>
									)}
								</div>
								<div className="p-col">
									{editing ? (
										<ButtonBase
											onClick={() => handleEdit()}
											className="greenButton"
										>
											Salvar
										</ButtonBase>
									) : null}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
