import { ButtonBase } from "@mui/material";
import { Divider } from "primereact/divider";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { MultiSelect } from "primereact/multiselect";
import React, { useEffect, useState } from "react";

import "./AddProduct.css";

import useProducts from "../../../../hooks/ProductHandlers";
import useProps from "../../../../hooks/PropsHandlers";

export default function AddProduct(props) {
	const [details, setDetails] = useState({
		reference: "",
		line: "",
		group: "",
		subgroup: "",
		registered_ERP: false,
		comments: "",
		goal_cost: "",
		target_price: "",
		product_base: "",
		collection: "",
		stylist: "",
		designer: "",
		tags: [],
		estimatives: [],
	});

	const [image, setImage] = useState();
	const [preview, setPreview] = useState();

	const {
		tags,
		lines,
		groups,
		collections,
		stylists,
		designers,
		productsBase,
	} = useProps();
	const { addProduct } = useProducts();

	useEffect(() => {
		props.backdropAutoClose(false);
	}, []);

	const handleSaving = () => {
		let postData = { ...details, tags: details.tags?.map((tag) => tag.id) };

		Object.keys(postData).forEach((key) => {
			if (postData[key]?.id !== undefined) {
				postData[key] = postData[key].id;
			}
		});

		addProduct(
			postData,
			image ? image : "",
			(res) => {
				if (props.setNewProduct) {
					props.setNewProduct(res.data);
				}
			},
			props.closeModal
		);
	};

	useEffect(() => {
		previewImg();
	}, [image]);

	const previewImg = async () => {
		if (!image) {
			setPreview(null);
			return;
		}
		let valueBase64 = await convertBase64(image);
		setPreview(valueBase64);
	};

	const convertBase64 = (file) => {
		return new Promise((resolve, reject) => {
			const fileReader = new FileReader();
			fileReader.readAsDataURL(file);

			fileReader.onload = () => {
				resolve(fileReader.result);
			};

			fileReader.onerror = (error) => {
				reject(error);
			};
		});
	};

	return (
		<div className="addProduct">
			<div className="p-grid p-justify-start">
				<div className="p-col imageContainer">
					{!image ? (
						<>
							<label
								className="greenButton"
								style={{ width: "15rem", fontSize: "1rem" }}
								htmlFor="selectButton"
							>
								Selecionar um arquivo
							</label>
							<input
								id="selectButton"
								style={{ display: "none" }}
								type="file"
								accept="image/*"
								onChange={(e) => {
									setImage(e.target.files[0]);
								}}
							/>
						</>
					) : (
						<>
							<img
								src={preview}
								alt="Imagem selecionada"
								style={{
									width: "15rem",
									borderRadius: "0.8rem",
									boxShadow: "0.1rem 0.8rem 0.9rem rgba(0, 0, 0, 0.1)",
								}}
							/>
							<label
								className="greenButton"
								style={{ width: "15rem", fontSize: "1rem", marginTop: "1rem" }}
								htmlFor="replaceButton"
							>
								Trocar imagem
							</label>
							<input
								id="replaceButton"
								style={{ display: "none" }}
								type="file"
								accept="image/*"
								onChange={(e) => {
									setImage(e.target.files[0]);
								}}
							/>
						</>
					)}
				</div>
				<div className="p-col">
					<div className="p-grid p-dir-col productDetails">
						<div className="p-col">
							<h4>Referência</h4>
							<InputText
								value={details.reference}
								onChange={(e) => {
									setDetails({ ...details, reference: e.target.value });
								}}
							/>
						</div>
						<div className="p-col">
							<h4>Tags</h4>
							<MultiSelect
								optionLabel="name"
								value={details.tags}
								options={tags}
								optionGroupLabel="name"
								optionGroupChildren="tags"
								display="chip"
								filter
								onChange={(e) => {
									setDetails({ ...details, tags: e.target.value });
								}}
							/>
						</div>
						<div className="p-col">
							<h4>Linha</h4>
							<Dropdown
								value={details.line}
								onChange={(e) => {
									setDetails({ ...details, line: e.value });
								}}
								options={lines}
								optionLabel="name"
								placeholder="Selecione uma linha"
							/>
						</div>
						<div className="p-col">
							<h4>Grupo</h4>
							<Dropdown
								value={details.group}
								onChange={(e) => {
									setDetails({ ...details, group: e.value });
								}}
								options={groups}
								optionLabel="name"
								placeholder="Selecione um grupo"
							/>
						</div>
						{details.group ? (
							<div className="p-col">
								<h4>Subgrupo</h4>
								<Dropdown
									value={details.subgroup}
									onChange={(e) => {
										setDetails({ ...details, subgroup: e.value });
									}}
									options={details.group.subgroups}
									optionLabel="name"
									placeholder="Selecione um subgrupo"
								/>
							</div>
						) : null}
						<div className="p-col">
							<h4>Coleção</h4>
							<Dropdown
								value={details.collection}
								onChange={(e) => {
									setDetails({ ...details, collection: e.value });
								}}
								options={collections}
								optionLabel="name"
								placeholder="Selecione uma coleção"
							/>
						</div>
						<div className="p-col">
							<h4>Estilista</h4>
							<Dropdown
								value={details.stylist}
								onChange={(e) => {
									setDetails({ ...details, stylist: e.value });
								}}
								options={stylists}
								optionLabel="name"
								placeholder="Selecione um estilista"
							/>
						</div>
						<div className="p-col">
							<h4>Designer</h4>
							<Dropdown
								value={details.designer}
								onChange={(e) => {
									setDetails({ ...details, designer: e.value });
								}}
								options={designers}
								optionLabel="name"
								placeholder="Selecione um designer"
							/>
						</div>
						<div className="p-col">
							<h4>Comentários</h4>
							<InputText
								value={details.comments}
								onChange={(e) => {
									setDetails({ ...details, comments: e.target.value });
								}}
							/>
						</div>

						<Divider />
						<div className="p-col">
							<h3>Pré-cadastro</h3>
						</div>

						<div className="p-col">
							<h4>Meta custo</h4>
							<InputNumber
								value={details.goal_cost}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								min={0}
								minFractionDigits={2}
								maxFracionDigits={2}
								onValueChange={(e) => {
									setDetails({ ...details, goal_cost: e.value });
								}}
							/>
						</div>
						<div className="p-col">
							<h4>Preço alvo</h4>
							<InputNumber
								value={details.target_price}
								currency="BRL"
								locale="pt-BR"
								mode="currency"
								showButtons
								min={0}
								minFractionDigits={2}
								maxFracionDigits={2}
								onValueChange={(e) => {
									setDetails({ ...details, target_price: e.value });
								}}
							/>
						</div>
						<div className="p-col">
							<h4>Base Produto</h4>
							<Dropdown
								value={{
									id: details?.product_base.id,
									name: details?.product_base.reference,
								}}
								onChange={(e) => {
									setDetails({ ...details, product_base: e.value });
								}}
								options={productsBase.map((e) => {
									return { id: e.id, name: e.reference };
								})}
								optionLabel="name"
								placeholder="Selecione uma base produto"
							/>
						</div>
						<div className="p-col">
							<ButtonBase
								onClick={(e) => handleSaving()}
								className="greenButton"
							>
								Adicionar
							</ButtonBase>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
