import { ButtonBase } from "@mui/material";
import React from "react";

import "./DuplicateItem.css";

import { ConfirmDialog, confirmDialog } from 'primereact/confirmdialog'; // To use <ConfirmDialog> tag

export default function DuplicateItem(props) {
	if (!props.item.product.generic) {
		return <>{props.children}</>
	}

	const confirm = () => {
		confirmDialog({
			message: `Tem certeza que deseja copiar o item ${props.item.product.reference}?`,
			header: 'Copiar',
			icon: 'pi pi-exclamation-triangle',
			accept: () => props.copyItem(props.item.id),
			reject: () => null
		});
	}

	return (
		<div className="icon-duplicate">
			<ConfirmDialog />
			<ButtonBase className="copy-button" onClick={confirm}>
				<i className="pi pi-copy" />
			</ButtonBase>
			{props.children &&
				<div>{props.children}</div>
			}
		</div>
	);
}
