import { useCallback, useEffect, useState } from 'react'
import useSWR from 'swr'
import api from '../services/api'
import { juntarArrays } from '../utils/arrays'

const fetchFiltered = async (url, filters) => {
    const fetchUrl = getFilteredUrl(url, filters)
    return api.get(fetchUrl).then(res => res.data).catch(err => console.log(err.response))
}

function getFilteredUrl (url, filters) {
    const tags = juntarArrays(
        filters.product_tags,
        filters.process_tags
    )

    url += checkFilter("product", filters.product)
    if (tags.length !== 0) url += `&tags=${tags?.map(tag => tag.id).join(',')}`;
    url += checkFilter("phase", filters.phase)
    url += checkFilter("typed", filters.typed)
    url += checkFilter("erp", filters.erp)
    url += checkFilter("generic", filters.generic)
    url += checkFilter("material", filters.material)
    url += checkFilter("store", filters.store)
    url += checkFilter("period", filters.period)
    url += checkFilter("line", filters.line)
    url += checkFilter("group", filters.group)
    url += checkFilter("subgroup", filters.subgroup)
    url += checkFilter("collection", filters.collection)
    url += checkFilter("stylist", filters.stylist)
    url += checkFilter("designer", filters.designer)
    url += checkFilter("ordering", filters.ordering)
        
    return url
}

function checkFilter (key, value) {
    if (value !== "" && value !== undefined && value !== null) {
        return `&${key}=${value}`
    }
    return ""
}

export default function useCompounds () {
    const [filters, setFilters] = useState({
        product: "",
        phase: "",
        store: "",
        period: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        product_tags: "",
        process_tags: "",
        stylist: "",
        designer: "",
        material: "",
        typed: null,
        erp: null,
        generic: null
    })

    const { data, error, mutate } = useSWR(
        ["/compounds/?", filters], 
        fetchFiltered,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const addItem = useCallback(async (postData, callback=null) => {
        return api.post(`/compounds/items`, postData).then((res) => {
            if (res.status === 200) {
                mutate()
                if (callback) {
                    callback()
                }
                return res
            }
        }).catch((err) => {
            let errorName = Object.keys(err.response.data)[0]
            alert(`Ocorreu um erro ao processar a solicitação: ${err.response.data[errorName]}`)
            return err.response
        })
    }, [])

    return {
        compounds: data,
        updateCompounds: mutate,
        isLoading: !error && !data,
        isError: error,
        compoundFilters: filters,
        addItem,
    }
}

export function useCompoundItem (id) {
    const { data, error, mutate } = useSWR(
        `/compounds/items/${id}`, 
        () => api.get(`/compounds/items/${id}`).then(res => res.data),
        {
            revalidateIfStale: false,
            revalidateOnFocus: false
        }
    )

    const patchItem = useCallback(async (id, patchData) => {
        const data = new FormData()
        data.append("product", patchData.product.id)
        data.append("observations", patchData.observations)
        data.append("period", patchData.period)
        data.append("typed", patchData.typed)
        data.append("generic_designer", patchData.generic_designer)

        return api.patch(`/compounds/items/${id}`, data).then(
        (res) => {
            if (res.status === 200) {
                mutate()
            }
            return res
        }).catch(
            err => {
                console.log(err.response)
                return err.response
            }
        )
    }, [])

    const patchImage = useCallback(async (id, image) => {
        const data = new FormData()
        data.append("image", image)

        api.patch(`/compounds/items/${id}`, data).then(
        (res) => {
            if (res.status === 200) {
                mutate()
                return res
            }
        }).catch(
            err => {
                console.log(err.response)
                return err.response
            }
        )
    }, [])

    const deleteItem = useCallback(async (id, callback=null) => {
        api.delete(`/compounds/items/${id}`).then((res) => {
            mutate()
            if (callback) callback();
            return res
        })
    }, [])

    const logItem = useCallback(async () => {
        return api.get(`/compounds/items/${id}/history`).then(res => res.data)
    }, [])

    return {
        item: data,
        updateItem: mutate,
        isLoading: !error && !data,
        isError: error,
        patchItem,
        patchImage,
        deleteItem,
        logItem,
    }
}


export const usePeriodItems = (id) => {
    const [filters, setFilters] = useState({
        product: "",
        phase: "",
        store: "",
        period: "",
        line: "",
        group: "",
        subgroup: "",
        collection: "",
        stylist: "",
        designer: "",
        product_tags: "",
        process_tags: "",
        material: "",
        typed: null,
        erp: null,
        generic: null,
        ordering: null
    })

    useEffect(() => {
        const filter = JSON.parse(localStorage.getItem("@greenmix:PERIOD_FILTERS"))
        if (filter) {
            filter.product = ""
            setFilters(filter)
        }
    }, [])

    useEffect(() => {
        localStorage.setItem("@greenmix:PERIOD_FILTERS", JSON.stringify(filters))
    }, [filters])

    const { data, error, mutate } = useSWR(
        id ? [`compounds/items`, `period=${id}`, filters] : null, 
        fetchFiltered,
        {
            revalidateIfStale: false,
            revalidateOnFocus: false,
        }
    )

    const filterItems = useCallback((key, value) => {
        setFilters(filters => {
            const updatedValue = {...filters}
            let is_null = Boolean(value == "" || value == undefined || value == null)

            if (key === "group" && is_null) {
                updatedValue["subgroup"] = ""
            }
    
            updatedValue[key] = value
    
            return updatedValue
        })
    }, [])

    const copyItem = useCallback(itemId => {
        api.post("compounds/items/duplicate", {item: itemId})
            .then(res => {mutate()})
            .catch(err => {console.log(err.response)})
    }, [])

    return {
        items: data,
        updateItems: mutate,
        isLoading: !error && !data,
        isError: error,
        filters,
        filterPeriod: filterItems,
        copyItem
    }
}