import React, { useEffect, useState } from 'react';

import { ButtonBase } from '@mui/material';
import { useSWRConfig } from "swr";
import api from '../../../../services/api';
import "./ManageImages.css";

const Image = (props) => {
    return (
        <img src={props.src} alt={props.alt} className="modalImage"/>
    )
}

export default function ManageImages(props) {
    const [images, setImages] = useState(props.product.images)
    const [upload, setUpload] = useState([])
    const [deleteImages, setDeleteImages] = useState([])
    const { mutate } = useSWRConfig()

    const handleSaving = () => {
        let promises = []
        const kb_size = 1024
        const max_size = 1000 * kb_size

        for (let i=0; i < upload.length; i++) {
            if (upload[i].size > max_size) {
                alert(`Uma ou mais imagens violam o limite máximo de ${max_size/kb_size} Kb`)
                return
            }
            
            const postData = new FormData()
            postData.append("product", props.product.id)
            postData.append("image", upload[i])

            promises.push(api.post("products/images", postData).catch(err => console.log(err.response)))
        }

        for (let i=0; i < deleteImages.length; i++) {
            promises.push(api.delete(`products/images/${deleteImages[i].id}`))
        }
        
        Promise.all(promises).then((res) => {
            mutate("/products/")
            props.closeModal()
        })
    }

    useEffect(() => {
        if (images.length > 0) {
            setImages(images.filter(img => !deleteImages.includes(img)))
        }
    }, [deleteImages])

    return(
        <div className='imageManager'>
            <h3>Imagens atuais</h3>
                <div className='imageContainer currentImages'>
                    {images?.map((image, index) => {
                        return (
                            <div className='imageHolder' key={index}>
                                <ButtonBase 
                                    className='deleteImage'
                                    onClick={() => {
                                        setDeleteImages([...deleteImages, image])
                                    }}
                                >x</ButtonBase>
                                <Image
                                    src={image.image} 
                                    alt={`Imagem para produto ${props.product.reference}`}
                                />
                            </div>
                        )
                    })}
                </div>
            <h3>Imagens para upload</h3>
            <div className='imageContainer'>
                {
                    upload?.map((image, index) => {
                        const kb_size = 1024

                        return (
                            <div className='imageHolder' key={index}>
                                <ButtonBase 
                                    className='deleteImage'
                                    onClick={() => {
                                        setUpload(upload.filter(img => img!==upload[index]))
                                    }}
                                >x</ButtonBase>
                                <img key={index} src={URL.createObjectURL(image)} alt="imagem" className='modalImage'/>
                                <p>{(image.size/kb_size).toFixed(1)} Kb</p>
                            </div>
                        )
                    })
                }
            </div>
            <div className='buttons'>
                <label className="selector" htmlFor='selectButton'>Selecionar um arquivo</label>
                <input id='selectButton'
                    type="file" accept='image/*' multiple
                    onChange={(e) => {
                        setUpload(Array.prototype.concat(upload, Array.from(e.target.files)))
                    }}
                />
                <button className='uploadButton' onClick={() => handleSaving()}>Salvar</button>
            </div>
        </div>
    )
}