import { useState } from "react";
import { useQuery } from "react-query";
import api from "../../services/api";
import "./GreenFiltersPage.css";

import { Dropdown } from "primereact/dropdown";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { logout, selectUser } from "../../app/slices/userSlice";
import { AddParamModal } from "./components/AddParamModal/AddParamModal";
import { GroupContainer } from "./components/GroupContainer/GroupContainer";
import { ViewFiltersModal } from "./components/ViewFiltersModal/ViewFiltersModal";

export const GreenFiltersPage = () => {
    const [store, setStore] = useState(null)
    const user = useSelector(selectUser)
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const { data: stores } = useQuery(["param_stores", user.id], () => api.get("filters/stores").then(res => res.data))
    const { data: params } = useQuery(
        ["params", "store", store], 
        () => api.get("filters/store/" + store).then(res => res.data),
        {
            enabled: store !== null 
        }
    )

    const log_out = () => {
        localStorage.removeItem("@greenMix:TOKENS")
        dispatch(logout({}))
        navigate('/', {replace: true})
    }

    return (
        <div id="gf-pg">
            <div className="navbar">
                <h3>Greenmix<b>Params</b></h3>
                <div style={{display: "flex", alignItems: "center", gap: "1rem"}}>
                    {user.greenfilters_access === "FULL" &&
                        <ViewFiltersModal />
                    }
                    {user.can_access_greenmix ?
                        <i className="pi pi-arrow-left" onClick={() => navigate("/products")}/>
                        :
                        <i className="pi pi-sign-out" onClick={log_out} />
                    }
                </div>
            </div>
            <main>
                <Dropdown
                    value={store}
                    onChange={(e) => {
                        setStore(e.value);
                    }}
                    options={stores}
                    optionLabel="name"
                    optionValue="id"
                    placeholder="Selecione uma filial"
                />
                <div>
                    {params &&
                        Object.keys(params).map(key => <GroupContainer key={key} group={key} params={params[key]} />)
                    }
                    {params && Object.keys(params).length === 0 &&
                        <h3>Nenhum parâmetro encontrado!</h3>
                    }
                </div>
            </main>
            {store && (user.greenfilters_access === "FULL" || user.greenfilters_access === "PARAMS") &&
                <AddParamModal store={store} />
            }
        </div>
    )
}