import 'primeflex/primeflex.css';
import { TabPanel, TabView } from 'primereact/tabview';
import React, { useState } from 'react';
import PeriodView from '../../components/PeriodView/PeriodView';
import './style.css';

export default function CompoundsPageContent (props) {
    const [activeIndex, setActiveIndex] = useState(0);

    if (!props.compound) return <h3>Nenhum composto encontrado!</h3> 

    return(
        <div className='compoundsPageContent'>
            <TabView 
                activeIndex={activeIndex} 
                onTabChange={(e) => setActiveIndex(e.index)}
                scrollable
            >
                {props.compound &&
                    props.compound.periods?.map((period) => (
                        <TabPanel 
                            header={period.name} key={period.id}
                        >
                            <PeriodView 
                                period={period} 
                                compoundId={props.compound.id} 
                                periodHooks={props.periodHooks}
                            />
                        </TabPanel>
                    ))
                }
            </TabView>
        </div>
    )
}