import React, { useEffect, useState } from 'react';

import { ProductCard } from '../../../ProductCard/ProductCard';
import EditSaveButton from '../../../shared/EditSaveButton/EditSaveButton';
import ModalBase from '../../../shared/ModalBase/ModalBase';
import ViewProduct from '../../Products/ViewProduct/ViewProduct';

import { ButtonBase } from '@mui/material';
import { confirmDialog } from 'primereact/confirmdialog';
import { Dropdown } from 'primereact/dropdown';

import { Calendar } from "primereact/calendar";
import { Checkbox } from 'primereact/checkbox';
import ViewLogProduct from "../../Products/ViewLogProduct/ViewLogProduct";
import { PhaseProductComments } from './PhaseProductComments/PhaseProductComments';
import { ProductRouteContainer } from './ProductRouteContainer/ProductRouteContainer';
import './ViewProductPhase.css';

export default function ViewProductPhase(props) {
    const [editing, setEditing] = useState(false)
    const [phase, setPhase] = useState(props.phase)
    const [showcase, setShowcase] = useState(props.data.showcase)
    const [newModeling, setNewModeling] = useState(props.data.new_modeling)
    const [date, setDate] = useState(props.data.limit_date)

    const handleEdit = () => {
        setEditing(true)
    }

    useEffect(() => {
        props.backdropAutoClose(!editing)
    }, [editing])

    const handleSave = () => {
        if (props.data && phase) {
            props.phaseOperations.patch(props.data, phase, showcase, newModeling, date)
            props.closeModal()
        } else {
            alert("Por favor verifique os dados inseridos")
        }
    }

    const confirmRemove = () => {
        confirmDialog({
            message: `Remover produto ${props.data.reference} da fase ${phase.phase}?`,
            header: 'Remover produto',
            icon: 'pi pi-exclamation-triangle',
            acceptLabel: 'Sim',
            rejectLabel: 'Não',
            accept: () => {
                props.phaseOperations.remove(props.data.id)
                props.closeModal()
            },
            reject: () => { }
        });
    }

    const handleCancel = () => {
        setPhase(props.phase)
        setShowcase(props.data.showcase)
        setEditing(false)
    }

    const parseDate = (date) => {
        if (!date) return 0

        let delta = date.split("-");
        let Ndate = new Date();
        Ndate.setDate(delta[0]);
        Ndate.setMonth(delta[1] - 1);
        Ndate.setFullYear(delta[2]);
        return Ndate;
    }

    return (
        <div className='viewProductPhase'>
            <div className="p-grid">
                <div className="p-col">
                    <div className='productContainer'>
                        <ModalBase
                            caller={<ProductCard data={props.data} doubleSize />}
                            content={<ViewProduct
                                productId={props.data.id}
                            />}
                            title={props.data.reference}
                        />
                    </div>
                </div>
                <div>
                    <div className='detailsContainer'>
                        <div style={{display: "flex", flexDirection: "column", gap: "0.5rem"}}>
                            <div>
                                <h4>Fase</h4>
                                <Dropdown value={phase}
                                    onChange={(e) => setPhase(e.target.value)}
                                    disabled={!editing}
                                    options={props.allPhases}
                                    optionLabel='phase'
                                    placeholder='Selecione uma fase'
                                />
                            </div>
                            <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                                <Checkbox inputId="cb2"
                                    onChange={e => setShowcase(e.checked)}
                                    checked={showcase}
                                    disabled={!editing}
                                />
                                <label>Mostruário</label>
                            </div>
                            <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                                <Checkbox inputId="cb3"
                                    onChange={e => setNewModeling(e.checked)}
                                    checked={newModeling}
                                    disabled={!editing}
                                />
                                <label>Modelagem nova</label>
                            </div>
                            <div>
                                <p>Data limite</p>
                                <Calendar style={{width: "100%"}}
                                    value={date && parseDate(date)}
                                    dateFormat="dd/mm/yy"
                                    disabled={!editing}
                                    onChange={(e) => {
                                        setDate(e.value ? `${e.value.getDate()}-${e.value.getMonth() + 1}-${e.value.getFullYear()}` : null)
                                    }}
                                />
                            </div>
                            <ProductRouteContainer productData={props.data} disabled={!editing} />
                        </div>
                        <div style={{marginTop: "1rem"}}>
                            <ModalBase
                                title="Log do Produto"
                                caller={
                                    <div className='yellowBorderButton'>
                                        <p>Log do produto</p>
                                    </div>
                                }
                                content={<ViewLogProduct productId={props.data.id} />}
                            />
                            <EditSaveButton onEdit={handleEdit} onSave={handleSave} onCancel={handleCancel} />
                            {!editing ?
                                <ButtonBase className='redButton' onClick={confirmRemove}>
                                    Remover
                                </ButtonBase> : null
                            }
                        </div>
                    </div>
                </div>
            </div>
            <PhaseProductComments prodId={props.data.id} />
        </div>
    )
}