import { Checkbox } from 'primereact/checkbox';
import { confirmDialog } from 'primereact/confirmdialog';
import { Dropdown } from "primereact/dropdown";
import { useState } from "react";
import { useMutation, useQuery, useQueryClient } from "react-query";
import api from "../../../../../services/api";


export function ProductRouteContainer({ productData, disabled }) {
    const qClient = useQueryClient()
    const [selectedRoute, setSelectedRoute] = useState(productData.phase_route)

    const { data: routes } = useQuery(
        "phase_routes",
        () => api.get("phases/routes").then(res => res.data)
    )

    const { data: prod_route, isLoading: isLoading_route, isRefetching } = useQuery(
        ["prod_route", productData.id],
        () => api.get(`products/${productData.id}/route`).then(res => res.data),
        {
            refetchOnWindowFocus: false
        }
    )

    const changeRoute = useMutation(
        (route_id) => api.patch(`products/${productData.id}/`, { phase_route: route_id }),
        {
            onMutate: async (route_id) => {
                setSelectedRoute(route_id)
                return {route_id}
            },
            onSettled: () => {
                qClient.invalidateQueries(["prod_route", productData.id])
            },
            onError: () => {
                setSelectedRoute(productData.phase_route)
            }
        }
    )

    const Node = ({ node }) => {
        const changeNodeCompletion = useMutation(
            (checked) => api.post(`products/${productData.id}/route`, [{ ...node, completed: checked }]),
            {
                onMutate: async (checked) => {
                    
                    await qClient.cancelQueries(["prod_route", productData.id])
                    const previousTodos = qClient.getQueryData(["prod_route", productData.id])
                    
                    qClient.setQueryData(["prod_route", productData.id], old => {
                        const copy = [...old]

                        let targetNode = copy.find(obj => obj.id === node.id)

                        if (targetNode) targetNode.completed = checked

                        return copy
                    })
                    return { previousTodos }
                },
                onError: (err, checked, context) => {
                    qClient.setQueryData(["prod_route", productData.id], context.previousTodos)
                },
                onSettled: () => {
                    qClient.invalidateQueries(["prod_route", productData.id])
                },
            }
        )

        return (
            <div style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                <Checkbox
                    onChange={e => changeNodeCompletion.mutate(e.checked)}
                    checked={node.completed}
                    disabled={disabled || isRefetching}
                />
                <p>{node.phase_name}</p>
            </div>
        )
    }

    const confirmRouteChange = (onAccept) => {
        confirmDialog({
            message: `Tem certeza que deseja trocar a rota de produto?`,
            header: 'Trocar rota',
            icon: 'pi pi-exclamation-triangle',
            acceptLabel: 'Sim',
            rejectLabel: 'Não',
            accept: () => {
                onAccept()
            },
            reject: () => { }
        });
    }

    return (
        <div>
            <p>Rotas</p>
            <Dropdown
                value={selectedRoute}
                onChange={(e) => {
                    confirmRouteChange(() => {
                        setSelectedRoute(e.value)
                        changeRoute.mutate(e.value)
                    })
                }}
                options={routes}
                optionLabel="name"
                optionValue="id"
                placeholder="Selecione uma rota"
                disabled={disabled}
            />
            <div style={{ padding: "0.5rem" }}>
                {prod_route && !isLoading_route &&
                    prod_route.map(node => <Node key={node.phase_id} node={node} />)
                }
            </div>
        </div>
    )
}