/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';

import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";

import { useMaterial } from '../../../../hooks/EstimativeHandlers';

import './DraftEstimative.css';

export default function DraftEstimative(props) {
    const { material } = useMaterial(props.matId)
    const currentColorsIds = props.currentColors.map(color => color.color_id)

    const [details, setDetails] = useState({
        color: "",
        assortment: 0,
        consumption: 0,
        product: props.prodId
    })

    useEffect(() => {
        props.setDraftData(props.id, details)
    }, [details])

    const SelectedMaterialTemplate = (option, props) => {
        if (option) {
            return (
                <div className="material-item">
                <p>{option.code} / <span className="materialDescription">{option.description}</span></p>
            </div>
            );
        }
    
        return (
            <span>
                {props.placeholder}
            </span>
        );
    }
    
    const MaterialOptions = (option) => {
        return (
            <div className="material-item">
                <p>{option.code} / <span className="materialDescription">{option.description}</span></p>
            </div>
        );
    }

    return (
        <tr className='mat-estimative draft'>
            <td className='mat-color'>
                <Dropdown
                    value={details.color}
                    onChange={(e) => {
                        setDetails(d => ({ ...d, color: e.value }));
                    }}
                    options={material?.colors.filter(color => {
                        return !currentColorsIds.includes(color.id)
                    })}
                    optionLabel="code"
                    placeholder="Selecione uma cor"
                    itemTemplate={MaterialOptions}
                    valueTemplate={SelectedMaterialTemplate}
                    filter
                    filterBy="code,description"
                />
            </td>
            <td>
                <InputNumber className='mat-consumption'
                    value={details.consumption}
                    locale="pt-BR"
                    mode="decimal"
                    showButtons
                    min={0}
                    minFractionDigits={2}
                    maxFracionDigits={2}
                    onValueChange={(e) => {
                        setDetails((d) => ({ ...d, consumption: e.value }));
                    }}
                />
            </td>
            <td>
                <InputNumber className='mat-assortment'
                    value={details.assortment}
                    locale="pt-BR"
                    suffix='%'
                    showButtons
                    min={0}
                    max={100}
                    onValueChange={(e) => {
                        setDetails((d) => ({ ...d, assortment: e.value }));
                    }}
                />
            </td> 
            <td id="cancel-draft" onClick={() => props.removeDraft()}>
                <i className='pi pi-times'/>
            </td>
        </tr>
    )
}