import React, { useEffect, useState } from "react";

import { Calendar } from "primereact/calendar";
import { InputNumber } from "primereact/inputnumber";
import useSWR from "swr";
import { useDemands } from "../../../../hooks/DemandsHandlers";
import api from "../../../../services/api";
import AddDemand from "../AddDemand/AddDemand";
import "./ListDemands.css";

export default function ListDemands(props) {
	const { demands, bookUpdateDemand, isLoading, isError } = useDemands(
		props.item.id
	);
	const { data: period } = useSWR(
		["period", props.item.period],
		() => api.get("compounds/periods/" + props.item.period).then(res => res.data)
	)
	const [editing, setEditing] = useState(false);
	const [New, setNew] = useState(false);
	const [dateRestriction, setDateRestriction] = useState({
		start: null,
		end: null,
	});
	const [details, setDetails] = useState();

	useEffect(() => {
		if (!isLoading && !isError) {
			setDetails(demands)
		}
	}, [demands]);

	const handleSaving = async () => {
		let res = await bookUpdateDemand(details);
		setEditing(!editing);
	};

	useEffect(() => {
		if (period) {
			let startDate = new Date(Date.parse(period.start_date));
			setDateRestriction((d) => ({ ...d, start: startDate }));

			let endDate = new Date(Date.parse(period.end_date));
			setDateRestriction((d) => ({ ...d, end: endDate }));
		}
	}, [period]);

	const Demand = (props) => {

		const parseDate = (date) => {
			if (!date) return 0

			let delta = date.split("-");
			let Ndate = new Date();
			Ndate.setDate(delta[0]);
			Ndate.setMonth(delta[1] - 1);
			Ndate.setFullYear(delta[2]);
			return Ndate;
		}

		return (
			<div className="demand" style={{ fontSize: "small" }}>
				<p style={{ width: "33%" }}>{props.data.store.name}</p>
				<Calendar
					value={parseDate(props.data.date)}
					dateFormat="dd/mm/yy"
					minDate={dateRestriction.start}
					maxDate={dateRestriction.end}
					placeholder="Data"
					disabled={!editing}
					onChange={(e) => {
						setDetails((old) => {
							return old.map((demand) => {
								if (props.data.id === demand.id) {
									demand.date = `${e.value.getDate()}-${e.value.getMonth() + 1
										}-${e.value.getFullYear()}`;
								}
								return demand;
							});
						});
					}}
					style={{ width: "33%" }}
				/>
				<Calendar
					value={parseDate(props.data.production_date)}
					dateFormat="dd/mm/yy"
					placeholder="Data de produção"
					disabled={!editing}
					onChange={(e) => {
						setDetails((old) => {
							return old.map((demand) => {
								if (props.data.id === demand.id) {
									demand.production_date = `${e.value.getDate()}-${e.value.getMonth() + 1
										}-${e.value.getFullYear()}`;
								}
								return demand;
							});
						});
					}}
					style={{ width: "33%" }}
				/>
				<InputNumber
					value={props.data.quantity}
					disabled={!editing}
					min={0}
					placeholder="Quantidade"
					minFractionDigits={0}
					maxFracionDigits={0}
					onValueChange={(e) => {
						setDetails((old) => {
							return old.map((demand) => {
								if (props.data.id === demand.id) {
									demand.quantity = e.value;
								}
								return demand;
							});
						});
					}}
					style={{ width: "33%" }}
				/>
			</div>
		);
	};

	return (
		<div className="listDemands">
			<div className="header">
				<p>Loja</p>
				<p>Data</p>
				<p>Data Produção</p>
				<p>Quantidade</p>
			</div>
			{details &&
				details?.map(demand => <Demand key={demand.id} data={demand} />)
			}
			{New && (
				<div>
					{period &&
						<AddDemand
							item={props.item}
							period={period}
							close={() => setNew(!New)}
						/>
					}
				</div>
			)}
			<div className="addContainer">
				{editing ? (
					<>
						<div
							className="addButton"
							onClick={() => {
								setNew(!New);
							}}
						>
							{!New ? (
								<div
									className="addButton"
									onClick={() => {
										setNew(!New);
									}}
								>
									+
								</div>
							) : (
								<div
									className="popButton"
									onClick={() => {
										setNew(!New);
									}}
								>
									-
								</div>
							)}
						</div>
						<div
							className="Button Cancel"
							onClick={() => {
								setNew(false);
								setEditing(!editing);
							}}
						>
							Cancelar
						</div>
						<div
							className="Button Save"
							onClick={() => {
								handleSaving();
							}}
						>
							salvar
						</div>
					</>
				) : (
					<>
						<div
							className="addButton"
							onClick={() => {
								setEditing(!editing);
							}}
						>
							<i className="pi pi-pencil" />
						</div>
					</>
				)}
			</div>
		</div>
	);
}
